<template>
  <div class="translate-task-wrap">
    <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>翻译管理</el-breadcrumb-item>
        <el-breadcrumb-item>翻译任务领取</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!--搜索-->
    <div class="search report">
      <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
        <el-form-item label="商品编码">
          <el-input v-model="searchForm.goodsSn" placeholder="商品编码" size="small"></el-input>
        </el-form-item>
        <el-form-item label="商家编码">
          <el-input v-model="searchForm.supplierCode"  size="small"></el-input>
        </el-form-item>
        <el-form-item label="商品品牌">
          <el-input v-model="searchForm.brandName" placeholder="商品品牌" size="small"></el-input>
        </el-form-item>
        <el-form-item label="提交时间始">
          <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.createStartDate" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="提交时间止" class="">
          <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.createEndDate" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item class="search-btn">
          <el-button type="primary" @click="search_smt">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
  
    <!-- 批量操作 -->
    <div class="doAction">
      <el-button type="primary" @click="taskAllot('')" size="small">批量领取</el-button>
    </div>
    <!-- 备注信息 -->
    <el-dialog title="备注信息" :visible.sync="dialogRemarkVisible" size="tiny" :close-on-click-modal="false">
      <el-form :model="remarkForm" ref="remarkForm">
        <el-form-item label="备注" label-width="80">
          <el-input v-model="remarkForm.remark" auto-complete="off" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogRemarkVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit_remark">确 定</el-button>
      </div>
    </el-dialog>
  
    <!-- 分配任务模态框 -->
    <el-dialog title="分配任务" :visible.sync="dialogAllotVisible" size="tiny" :close-on-click-modal="false">
      <el-form ref="userForm" :model="userForm" label-width="80px">
        <el-form-item label="人员列表" class="userlist">
          <el-select v-model="userForm" placeholder="请选择" @change="userChange">
            <el-option v-for="item in userList" :key="item.account" :label="item.account" :value="item">
            </el-option>
          </el-select>
          <div class="el-form-item__error" v-if="user_error">请选择人员</div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogAllotVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit_allot">确 定</el-button>
      </span>
    </el-dialog>
    <!--翻译列表-->
    <el-table :data="taskList" stripe style="width: 100%" v-loading="loading" @selection-change="handleSelectionChange" border>
      <el-table-column type="selection" width="55" :selectable="showCk"></el-table-column>
      <el-table-column label="商家编码" prop="supplierCode" width="180" show-overflow-tooltip align="center"></el-table-column>
      <el-table-column label="商品编码" prop="goodsSn"  align="center" min-width="150" show-overflow-tooltip></el-table-column>
      <el-table-column label="商品名称" prop="goodsTitle" min-width="180" show-overflow-tooltip align="center"> </el-table-column>
      <el-table-column label="商品品牌" prop="brandName" min-width="150" show-overflow-tooltip align="center"> </el-table-column>
      <el-table-column label="提交翻译时间" align="center" width="180">
        <template slot-scope="scope">{{scope.row.createTime|dateFormat }}</template>
      </el-table-column>
      <el-table-column label="操作" width="200" fixed="right" align="center">
        <template slot-scope="scope">
          <i title="查看" class="primary iconfont icon-chakan" @click="doAction(scope.row.taskId,scope.row.taskStatus)"></i>
          <i title="领取" class="primary iconfont icon-allot" @click="taskAllot(scope.row.taskId)" v-if="scope.row.taskStatus==1"></i>
        </template>
      </el-table-column>
    </el-table>
  
    <!--分页-->
    <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
      <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.pageSize" layout="total, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
  
  </div>
</template>
<style lang="less" scoped>
.translate-task-wrap {
  padding-right: 40px;
}

.doAction {
  padding: 10px; // height: 48px;
}
</style>
<style lang="less">
.userlist label::before {
  content: '*';
  color: #ff4949;
  margin-right: 4px;
}
</style>


<script>
import { mapGetters } from 'vuex'
import format from 'date-format';
export default {
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          if (time.getTime() > Date.now()) {
            return time
          } else {
            return false;
          }

        }
      },
      searchForm: {  
        "goodsSn": "",
        "goodsTitle": "",
        "instId": 0,
        instName: '',
        "taskId": "",
        "taskStatus": 1,
        createStartDate:'',
        "createEndDate": '',
        "translateDay": "",
        auditStartDate: '',
        auditEndDate: '',
        page: 1,
        pageSize: this.myConfig.pageRows
      },
      loading: false,
      //翻译列表列表
      taskList: [],
      total: 0,
      //选择数据
      selectData: [],
      userList: [],
      dialogAllotVisible: false,
      userForm: {},
      allotIds: [],
      user_error: false,
      selectStatus: 0,
      //备注信息
      dialogRemarkVisible: false,
      remarkForm: {
        remark: ''
      },
      changeMyStatus: 1,
      changeMyIds: []

    }
  },
  computed: {
    renderStatus(data) {
      return 555;
    }
  },
  filters: {
    renderStatus(status) {
      switch (status) {
        case 1:
          return '未分配';
          break;
        case 2:
          return '翻译中';
          break;
        case 3:
          return '审核中';
          break;
        case 4:
          return '已完成';
          break;
        case 5:
          return '审核不通过';
          break;
        case 6:
          return '驳回';
          break;

      }
    },
    dateFormat(date) {
      return format('yyyy-MM-dd hh:mm', new Date(date));
    }
  },
  computed: {
    ...mapGetters({
      authSelect: 'getAuthSelect',
      authOps: 'getAuthOps'
    })
  },
  methods: {
    //搜索
    search_smt() {
      if (this.searchForm.createStartDate != '') {
        this.searchForm.createStartDate = format('yyyy-MM-dd', new Date(this.searchForm.createStartDate));
      }
      if (this.searchForm.createEndDate != '') {
        this.searchForm.createEndDate = format('yyyy-MM-dd', new Date(this.searchForm.createEndDate));
      }
      this.selectStatus = this.searchForm.taskStatus;
      this.selectData = [];
      this.searchForm.page = 1;
      this.selectStatus = this.searchForm.taskStatus;
      //去掉前后空格
      for(let i in this.searchForm){
        if(typeof this.searchForm[i]=='string'){
           this.searchForm[i]= this.searchForm[i].trim();
        }
      }
      this.getTaskList();
    },
    //任务列表获取
    getTaskList() {
      this.axios.post(this.myConfig.code.proxy + '/translate/api/task/query', { body: this.searchForm }).then(response => {
        let data = response.data;
        if (data.head.code == '10000') {
          this.taskList = data.body.rows;
          this.total = data.body.total;
          this.$store.dispatch('setTranslateSearch',{type:'allotTask',data:JSON.parse(JSON.stringify(this.searchForm))});
        }
      })
    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page = val;
      this.getTaskList();
    },
    //翻译跳转
    doAction(id, status) {
      this.$router.push('/translate/action/' + id + '/' + status + '/allot');
    },
    showCk(row, index) {
      return true;
      // console.log(row, index)
    },
    //复选框
    handleSelectionChange(val) {
      this.selectData = val;
    },
    //批量变更状态
    changeStatus(id, status) {
      let ids = [];
      this.changeMyStatus = status;
      if (id == '') {
        for (let i in this.selectData) {
          ids.push(this.selectData[i].taskId)
        }
      } else {
        ids = [id];
      }
      this.changeMyIds = ids;
      if (ids.length == 0) {
        this.$message({
          message: '请选择任务',
          type: 'error'
        })
        return false;
      }
      if (status == 5 || status == 6) {
        this.dialogRemarkVisible = true;
      } else {
        this.axios.post(this.myConfig.code.proxy + '/translate/api/change/status', { body: { taskStatus: this.changeMyStatus, taskIds: this.changeMyIds } }).then((res) => {
          if (res.data.head.code == '10000') {
            this.$message({
              message: '操作成功',
              type: 'success'
            })
            this.getTaskList();
          }
        })
      }
    },
    //提交备注信息
    submit_remark() {
      this.axios.post(this.myConfig.code.proxy + '/translate/api/change/status', { body: { taskStatus: this.changeMyStatus, taskIds: this.changeMyIds, remark: this.remarkForm.remark } }).then((res) => {
        if (res.data.head.code == '10000') {
          this.dialogRemarkVisible = false;
          this.$message({
            message: '操作成功',
            type: 'success'
          })
          this.getTaskList();
        }
      })
    },

    //领取任务
    taskAllot(id) {
      this.userList = [];
      this.userForm = {};
      this.user_error = false;

      if (id) {
        this.allotIds = [id]
      } else {
        let ids = [];
        for (let i in this.selectData) {
          ids.push(this.selectData[i].taskId)
        }
        this.allotIds = ids.concat([]);
      }
      if (this.allotIds.length == 0) {
        this.$message({
          message: '请选择任务',
          type: 'error'
        })
        return false;
      }
      this.axios.post(this.myConfig.code.proxy + '/translate/api/task/allot', { body: { taskIds: this.allotIds, type: 'GET' } }).then((res) => {
        let data = res.data;
        if (data.head.code == '10000') {
          this.$message({
            message: '操作成功',
            type: 'success'
          })
          this.getTaskList();
        }
      })

    },
    // 提交分配任务
    submit_allot() {
      let tmp = JSON.parse(JSON.stringify(this.userForm));
      tmp.type = "ALLOT";
      tmp.taskIds = this.allotIds;
      if (JSON.stringify(this.userForm) == '{}') {
        this.user_error = true;
        return;
      }
      this.axios.post(this.myConfig.code.proxy + '/translate/api/task/allot', { body: tmp }).then((res) => {
        let data = res.data;
        if (data.head.code == '10000') {
          if (data.head.code == '10000') {
            this.$message({
              message: '操作成功',
              type: 'success'
            })
            this.dialogAllotVisible = false;
            this.getTaskList();
          }
        }
      })

    },
    //翻译人员选择事件
    userChange() {
      if (JSON.stringify(this.userForm) == '{}') {
        this.user_error = true;
      } else {
        this.user_error = false;
      }
    },



  },
  created() {
    let geter=JSON.parse(JSON.stringify(this.$store.getters.getTranslateSearch)).allotTask;
    this.searchForm=Object.assign(this.searchForm,geter);
    this.getTaskList();

  }
}
</script>





