//翻译模块 main sys components
import SysMain from '@/components/sys/main.vue';
import Task from '@/components/sys/translate/TranslateTask.vue';
import Action from '@/components/sys/translate/TranslateTask-action.vue'
import Inst from '@/components/sys/translate/TranslateInst.vue'
import CharStats from '@/components/sys/translate/Translate-char-stats.vue'
import AuditStats from '@/components/sys/translate/Translate-audit-stats.vue'
import AllotTask from '@/components/sys/translate/Translate-allotTask.vue'
import MyTask from '@/components/sys/translate/Translate-myTask.vue'
import Auto from '@/components/sys/translate/Translate-auto.vue'
//任务管理
let router_task = {
  path: 'task/:type',
  name: 'translate-task',
  component: Task
}

//翻译商品
let router_action = {
  path: 'action/:id/:status/:type',
  name: 'translate-action',
  component: Action
}

//翻译机构
let router_translateInst = {
  path: 'translate-inst',
  name: 'translate-inst',
  component: Inst
}
//翻译字符统计
let router_translateCharStats = {
  path: 'char-stats',
  name: 'translate-char-stats',
  component: CharStats
}
//翻译审核统计
let router_translateAuditStats = {
  path: 'audit-stats',
  name: 'translate-audit-stats',
  component: AuditStats
}
//领取任务
let router_translate_allotTask = {
  path: 'allotTask',
  name: 'translate-allotTask',
  component: AllotTask
}
//我的任务列表
let router_translate_myTask = {
  path: 'myTask',
  name: 'translate-myTask',
  component: MyTask
}
//机器翻译
let router_translate_auto = {
  path: 'auto',
  name: 'translate-auto',
  component: Auto
}
let router_translate = {
  path: 'translate',
  name: 'translate',
  component: SysMain,
  children: [router_task, router_action, router_translateInst, router_translateCharStats,router_translateAuditStats, router_translate_allotTask,router_translate_myTask,router_translate_auto]

}

export default router_translate;


