export default {
    //获取权限
    getAuthority(state) {
        return state.authority
    },
    getAuthSelect(state) {
        return state.authSelect
    },
    getAuthOps(state) {
        return state.authOps
    },
    getCollapse(state) {
        return state.collapse;
    },
    //获取滚动距离
    getScrollTop(state) {
        return state.scrollTop
    },
    //获取翻译的状态与页码
    getTranslateManage(state) {
        return state.translate.manage
    },
    getTranslateMyTask(state) {
        return state.translate.myTask
    },
    getTranslateSearch(state) {
        return state.translateSearch
    },
    // 公告处理
    // 商品有效期公告
    getNoticeTerm(state) {
        console.log(state.notice, 11111111111111)
        return state.notice
    },
    // 商品有效期公告
    getGoodsBatch(state) {
        return state.goods.batchIds
    }
}


