<template>
  <div class="iframe" v-loading="loading">
    <iframe :src="uri" width="100%" height="100%" @load="loaded" id="vendor-iframe" ></iframe>
  </div>
</template>

<style lang="less" scoped>
.iframe {
  height: 100%;
  overflow: hidden;
}
</style>

<script>
export default {
  data() {
    return {
      uri: '',
      loading:true,
      uri_aa:JSON.parse(localStorage.getItem('hrf')).uri
    }
  },
  computed:{
    
  },
  methods: {
    loaded(){
      this.loading=false;
    },
    fetchDate() {
      this.loading=true;
      let uri=JSON.parse(localStorage.getItem('hrf')).uri;
      let uriArr=uri.split('/')
      if (uri.indexOf('http') != -1 || uri.indexOf('https') != -1) {
        this.uri = uri;
      } else if(uriArr[1]=='fv') {
        this.uri =this.myConfig.code.fv +uri+'&rd='+JSON.parse(localStorage.getItem('hrf')).time;
      }else if(uriArr[1]=='goods') {
        if(uri.indexOf('notice')!=-1){
          this.uri =uri+'&rd='+JSON.parse(localStorage.getItem('hrf')).time;
        }else{
            this.uri =uri+'?rd='+JSON.parse(localStorage.getItem('hrf')).time;
        }
      }
    },
    
  },
  created() {
    let uri=JSON.parse(localStorage.getItem('hrf')).uri;
    let uriArr=uri.split('/')
    if (uri.indexOf('http') != -1 || uri.indexOf('https') != -1) {
      this.uri = uri;
    } else if(uriArr[1]=='fv') {
      this.uri =this.myConfig.code.fv +uri+'&rd='+JSON.parse(localStorage.getItem('hrf')).time;
    }else if(uriArr[1]=='goods') {
      if(uri.indexOf('notice')!=-1){
        this.uri =uri+'&rd='+JSON.parse(localStorage.getItem('hrf')).time;
      }else{
          this.uri =uri+'?rd='+JSON.parse(localStorage.getItem('hrf')).time;
      }
     
    }

  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    "$route": "fetchDate",
  },
}
</script>



