<template>
    <div class="wrap-action wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>安全管理</el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/sys/menuMgmt' }">菜单管理</el-breadcrumb-item>
                <el-breadcrumb-item>操作管理</el-breadcrumb-item>
            </el-breadcrumb>
            <div class="add-menu fl-r" >
            <el-button type="warning" class="add-menu-btn" @click="showAddForm=true" size="mini">
                    新增操作</el-button>
            </div>
        </div>
        <div class="action-list">
    
            <!--新增操作表单-->
            <el-dialog title='给菜单"CPGL"添加操作权限' :visible.sync="showAddForm" class="add-action-form" :close-on-click-modal="false" :before-close="showAddFormClose">
                <div class="add-btn">
                    <span class="tip">批量最多10个</span>
                    <i class="iconfont icon-add" @click="addList"></i>
                </div>
                <el-form :model="addForm" label-width="100px" :inline="true" class="addForm-box" ref="addForm">
                    <div class="action-item-list clear-fix" v-for="(val,index) in addForm.ops " :key="val.key">
                        <el-form-item class="list-item" label="操作名称" :prop="'ops.' + index + '.operationName'" :rules="{required: true, message: '操作名称不能为空'}">
                            <el-input v-model="val.operationName" auto-complete="off" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="操作Uri" :prop="'ops.' + index + '.operationUri'" :rules="{required: true, message: '操作Uri不能为空'}">
                            <el-input v-model="val.operationUri" auto-complete="off" size="small"></el-input>
                        </el-form-item>
                        <el-button class="list-item" type="danger" size="small" @click="doDeleteActionList(val)" v-if="authOps.includes('/auth/api/menu/op/delete')">删除</el-button>
                    </div>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="showAddForm = false">取 消</el-button>
                    <el-button type="primary" @click="submit_addForm">确 定</el-button>
                </div>
            </el-dialog>
    
            <!--操作列表-->
            <el-table :data="actionList" stripe style="width: 100%" v-loading="loading">
                <el-table-column prop="operationId" label="操作流水号" width="180">
                </el-table-column>
                <el-table-column prop="menuId" label="菜单编码" width="180">
                </el-table-column>
                <el-table-column prop="operationName" label="操作名称">
                </el-table-column>
                <el-table-column prop="operationUri" label="操作Uri">
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <i title="删除" class="danger iconfont icon-delete" @click="doDeleteAction(scope.row.operationId)"></i>
                    </template>
                </el-table-column>
            </el-table>
    
            <!--分页-->
            <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="actionSearch.page" :page-size="actionSearch.rows" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>


<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}

.add-menu {
    height: 48px;
    text-align: right;
}

.add-btn {
    padding-bottom: 15px;
    padding-right: 15px;
    text-align: right;
    height: 54px;
    line-height: 54px;
    span,
    i {
        display: inline-block;
        vertical-align: middle;
    }
    i {
        font-size: 36px;
        cursor: pointer;
    }
}
</style>
<style lang="less">
.add-action-form {
    .action-item-list {
        .el-form-item {
            float: left;
            &.button {
                margin-left: 10px;
                margin-top: 4px;
            }
        }
    }
    .el-dialog--small {
        padding: 10px 20px;
        width: 60%;
        .addForm-box {
            max-height: 300px;
            overflow-x: auto;
        }
    }
}
</style>



<script>
import { mapGetters } from 'vuex'
export default {
    data() {
        return {

            //loading...
            loading: true,

            //菜单id
            menuId: '',

            //操作列表
            actionList: [],
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            addForm: {
                ops: [
                    {
                        "operationName": "",
                        "operationUri": ""
                    }
                ]
            },
            showAddForm: false,
            total: 0,
        }
    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps'
        }),
        //操作列表查询
        actionSearch() {
            return {
                menuId: this.menuId,
                page: 1,
                rows: this.myConfig.pageRows
            }
        }

    },
    methods: {
        //菜单操作列表查询/menu/op/list
        getActionList() {
            let that = this;
            this.loading = true;
            that.axios.post(this.myConfig.code.auth + '/api/menu/op/list', { body: this.actionSearch }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.actionList = data.body.rows;
                    this.total = data.body.total;
                }
            }).catch(error => {
                console.log(error)
            })
        },
        //添加操作权限 input
        addList() {
            if (this.addForm.ops.length > 9) {
                this.$message({
                    type: "error",
                    message: '批量最多10个',
                    duration: 1000
                })
            } else {
                this.addForm.ops.push({
                    "operationName": "",
                    "operationUri": ""
                })
            }

        },
        //删除操作权限  input
        doDeleteActionList(item) {
            var index = this.addForm.ops.indexOf(item)
            if (index !== -1) {
                this.addForm.ops.splice(index, 1)
            }
        },
        //菜单关闭前
        showAddFormClose(done) {
            done();
            this.addForm = {
                ops: [
                    {
                        "operationName": "",
                        "operationUri": ""
                    }
                ]
            }

        },
        //提交 添加操作权限表单
        submit_addForm() {
            //表单验证
            this.$refs.addForm.validate((validate) => {
                if (validate) {
                    this.axios.post(this.myConfig.code.auth + '/api/menu/op/save', {
                        body: {
                            menuId: this.menuId,
                            ops: this.addForm.ops
                        }
                    }).then(response => {
                        let data = response.data;
                        if (data.head.code == '10000') {
                            this.showAddForm = false;
                            this.getActionList();
                        }

                    }).catch(error => {
                        console.log(error)
                    })
                }
            })
        },
        //删除操作权限
        doDeleteAction(id) {

            console.log(id)
            let that = this;
            this.$confirm('您确定删除此操作?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                that.axios.post(this.myConfig.code.auth + '/api/menu/op/delete', { body: { operationId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//删除成功
                        that.getActionList();
                        that.$message({
                            type: "success",
                            message: '删除成功',
                            duration: 1000
                        })
                    } else {//新增失败
                        that.$message({
                            type: "error",
                            message: data.body.sub_msg,
                            duration: 1000
                        })
                    }
                }).catch(error => {
                    console.log(error)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除',
                    duration: 1000
                });
            });
        },
        //分页
        handleCurrentChange(val) {
            this.actionSearch.page = val;
            this.getActionList();
        }
    },
    created() {
        let that = this;
        this.menuId = this.$route.params.id;
        this.getActionList();
    }
}
</script>



