<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>安全管理</el-breadcrumb-item>
                <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            </el-breadcrumb>

            <!--新增用户-->
            <div class="add-menu fl-r" v-if="authOps.includes('/auth/api/user/save')">
                <el-button type="warning" class="add-menu-btn" @click="addForm_btn('')" size="mini">
                    <i class="iconfont icon-add"></i>新增用户</el-button>
            </div>
        </div>

        <div class="search">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline">
                <i class="el-icon-search"></i>
                <el-form-item label="用户名">
                    <el-input v-model="searchForm.userName" placeholder="用户名" size="small"></el-input>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt">查询</el-button>
                </el-form-item>
            </el-form>
        </div>

        <div class="menu-list">

            <!--新增管理员表单-->
            <el-dialog :title="userId!=''?'修改管理员':'新增管理员'" :visible.sync="dialogAddForm" size="tiny" :close-on-click-modal="false">
                <el-form :model="addForm" label-width="100px" ref="addForm" :rules="addFormRules">
                    <el-form-item label="用户名" prop="userAccount" :rules="{required:true,message:'请输入用户名'}">
                        <el-input v-model="addForm.userAccount" placeholder="用户名" :disabled="userId!=''"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" v-if="userId==''" prop="userPassword" :rules="{required:true,message:'请输入密码'}">
                        <el-input v-model="addForm.userPassword" placeholder="密码" type="password"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" v-if="userId==''" prop="userPasswordCheck" class="userPasswordCheck">
                        <el-input v-model="addForm.userPasswordCheck" placeholder="确认密码" type="password"></el-input>
                    </el-form-item>
                    <el-form-item label="昵称" prop="userName" :rules="{required:true,message:'请输入昵称'}">
                        <el-input v-model="addForm.userName" placeholder="昵称"></el-input>
                    </el-form-item>
                    <el-form-item label="分配角色" prop="roleId" :rules="{required:true,message:'请输入选择角色'}">
                        <el-select v-model="addForm.roleId" placeholder="请选择角色">
                            <el-option :label="val.roleName" :value="val.roleId" v-for="(val,key) in roleList" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="手机" prop="phone" :rules="{required:true,message:'请输入手机号码'}">
                        <el-input v-model="addForm.phone" placeholder="手机"></el-input>
                    </el-form-item>
                    <el-form-item label="email" prop="email" :rules="[{ required: true, message: '请输入邮箱地址' },{ type: 'email', message: '请输入正确的邮箱地址' }]">
                        <el-input v-model="addForm.email" placeholder="email"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogAddForm = false">取 消</el-button>
                    <el-button type="primary" @click="submit_adduser">确 定</el-button>
                </div>
            </el-dialog>

            <!--管理员列表-->
            <el-table :data="userList" stripe style="width: 100%" v-loading="loading" border>
                <el-table-column prop="userId" label="ID" align="center"></el-table-column>
                <el-table-column prop="userAccount" label="用户名" align="center"></el-table-column>
                <el-table-column prop="userName" label="昵称" align="center"></el-table-column>
                <el-table-column prop="roleName" label="角色" align="center"></el-table-column>
                <el-table-column prop="lastLoginTime" label="最后登录时间" align="center"></el-table-column>
                <el-table-column label="操作" width="250">
                    <template slot-scope="scope">

                        <i title="详情" class="primary iconfont icon-chakan" @click="doAction(scope.row.userId)" v-if="authOps.includes('/auth/api/user/detail')"></i>
                        <i title="修改" class="primary iconfont icon-update" @click="doUpdate(scope.row.userId)" v-if="authOps.includes('/auth/api/user/update')"></i>
                        <i title="重置密码" class="primary iconfont icon-reset" @click="doReset(scope.row.userId)" v-if="authOps.includes('/auth/api/user/update')"></i>
                        <i title="删除" class="danger iconfont icon-delete" @click="doDelete(scope.row.userId)" v-if="authOps.includes('/auth/api/user/delete') && scope.row.isAdmin==0 "></i>
                    </template>
                </el-table-column>
            </el-table>

            <!--分页-->
            <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.rows" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>

            <!--管理员详情-->
            <el-dialog title="管理员详情" :visible.sync="dialogUserDetail" class="dialogUserDetail" size="tiny" :close-on-click-modal="false">
                <div class="list-box ">
                    <div class="list ">
                        <span class="label">操作员编号</span>
                        <span class="txt">{{addForm.userId}}</span>
                    </div>
                    <div class="list ">
                        <span class="label">用户名</span>
                        <span class="txt">{{addForm.userAccount}}</span>
                    </div>
                    <div class="list ">
                        <span class="label">操作员昵称</span>
                        <span class="txt">{{addForm.userName}}</span>
                    </div>
                    <div class="list ">
                        <span class="label">角色编号</span>
                        <span class="txt">{{addForm.roleId}}</span>
                    </div>
                    <div class="list ">
                        <span class="label">手机</span>
                        <span class="txt">{{addForm.phone}}</span>
                    </div>
                    <div class="list ">
                        <span class="label">email</span>
                        <span class="txt">{{addForm.email}}</span>
                    </div>
                </div>
            </el-dialog>

        </div>

    </div>
</template>

<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<style lang="less">
.dialogUserDetail {
    .list {
        height: 36px;
        line-height: 36px;
        .label {
            display: inline-block;
            width: 100px;
            text-align: right;
            margin-right: 60px;
            color: #A5C5EF;
        }
        .txt {
            color: #222;
        }
    }
}

.userPasswordCheck {
    label {
        &::before {
            content: '*';
            color: #ff4949;
            margin-right: 4px;
        }
    }
}
</style>


<script>
import { mapGetters } from 'vuex'
export default {
    data() {
        let checkPwd = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('请再次输入密码'));
            }
            setTimeout(() => {
                if (value !== this.addForm.userPassword) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            }, 500);
        };
        return {
            //加载
            loading: true,
            //用户查询
            searchForm: {
                userName: '',
                page: 1,
                rows: this.myConfig.pageRows
            },

            //用户列表
            userList: [],

            //用户id
            userId: [],

            //角色列表
            roleList: [],
            total: 0,
            //新增用户
            addForm: {
                email: "",
                phone: "",
                roleId: "",
                userAccount: "",
                userName: "",
                userPassword: "",
                userPasswordCheck: ""
            },

            //表单验证
            addFormRules: {
                userPasswordCheck: [{ validator: checkPwd }]

            },
            dialogAddForm: false,
            dialogUserDetail: false,
            userId: ""
        }
    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps'
        })
    },

    methods: {

        //用户查询
        search_smt() {
            this.searchForm.page = 1;
            this.getuserList();
        },

        //用户列表获取
        getuserList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.auth + '/api/user/list', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.userList = data.body.rows;
                    this.total = data.body.total;
                }
            }).catch(error => {
                console.log(error)
            })
        },

        //获取用户详情
        getuserDetail(id, param) {

            this.axios.post(this.myConfig.code.auth + '/api/user/detail', { body: { userId: id } }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.addForm = data.body;
                    if (param == 'detail') {
                        this.dialogUserDetail = true;
                    } else {
                        this.dialogAddForm = true;
                    }
                }
            })

        },

        //获取角色列表
        getRoleList() {
            this.axios.post(this.myConfig.code.auth + '/api/role/combobox', {}).then(response => {
                let data = response.data;
                if (data.head.code = "10000") {
                    this.roleList = data.body.rows;
                }
            }).catch(error => {
                console.log(error)
            })
        },
        //新增用户
        addForm_btn() {
            this.userId = '';
            this.getRoleList();
            this.dialogAddForm = true;
            this.addForm = {
                email: "",
                phone: "",
                roleId: "",
                userAccount: "",
                userName: "",
                userPassword: "",
                userPasswordCheck: ""
            };
        },

        //新增用户表单提交
        submit_adduser() {
            this.$refs.addForm.validate((validate) => {
                if (validate) {
                    if (this.userId == '') {//新增用户
                        this.axios.post(this.myConfig.code.auth + '/api/user/save', { body: this.addForm }).then(response => {
                            let data = response.data;
                            if (data.head.code === '10000') {//新增成功
                                this.getuserList();
                                this.dialogAddForm = false;
                            }
                        }).catch(error => {
                            console.log(error)
                        })
                    } else {//修改用户
                        this.axios.post(this.myConfig.code.auth + '/api/user/update', { body: this.addForm }).then(response => {
                            let data = response.data;
                            if (data.head.code === '10000') {//修改成功
                                this.getuserList();
                                this.dialogAddForm = false;
                                this.$router.go(0);
                            }
                        }).catch(error => {
                            console.log(error)
                        })
                    }
                }
            })

        },

        //修改用户
        doUpdate(id) {
            this.userId = id;
            this.getRoleList();
            this.getuserDetail(id);
        },
        //重置密码
        doReset(id) {
            this.$confirm('您确定重置密码?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.axios.post(this.myConfig.code.proxy + '/auth/api/user/resetPwd', { body: { userId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//删除成功
                        this.getuserList();
                        this.$message({
                            type: "success",
                            message: '重置成功',
                            duration: 1000
                        })
                    } else {//新增失败
                        this.$message({
                            type: "error",
                            message: data.body.sub_msg,
                            duration: 1000
                        })
                    }
                })
            })
        },
        //删除用户
        doDelete(id) {
            this.$confirm('您确定删除此用户?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.axios.post(this.myConfig.code.auth + '/api/user/delete', { body: { userId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//删除成功
                        this.getuserList();
                        this.$message({
                            type: "success",
                            message: '删除成功',
                            duration: 1000
                        })
                    } else {//新增失败
                        this.$message({
                            type: "error",
                            message: data.body.sub_msg,
                            duration: 1000
                        })
                    }
                }).catch(error => {
                    console.log(error)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除',
                    duration: 1000
                });
            });
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page = val;
            this.getuserList();
        },
        //管理员详情
        doAction(id) {
            this.getuserDetail(id, 'detail');
        }

    },
    created() {
        this.getuserList();
    }
}
</script>




