<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>物流管理</el-breadcrumb-item>
                <el-breadcrumb-item>待分包裹管理列表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
    
        <!-- 搜索 -->
        <div class="search">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline">
                <i class="el-icon-search"></i>
                <el-form-item label="下单时间起">
                    <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.createTimeBegin" :picker-options="pickerOptions"></el-date-picker>
                </el-form-item>
                <el-form-item label="下单时间止">
                    <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.createTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                </el-form-item>
                <el-form-item label="订单号">
                    <el-input v-model="searchForm.orderCode" placeholder="订单号" size="small"></el-input>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt" size="">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--包裹列表-->
        <el-table :data="orderList" stripe style="width: 100%" v-loading="loading">
            <el-table-column label="订单号" prop="orderCode" align="center" width="180"></el-table-column>
            <el-table-column label="订单状态" align="center">
                <template slot-scope="scope">{{scope.row.orderStatus|status}}</template>
            </el-table-column>
            <el-table-column label="所属商家" prop="merchantCode" align="center" width="150"> </el-table-column>
            <el-table-column label="下单时间" align="center">
                <template slot-scope="scope">
                    {{scope.row.orderTime|dateFormat}}
                </template>
            </el-table-column>
    
            <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                    <i title="查看" class="primary iconfont icon-chakan" @click="doAction(scope.row.orderCode)"></i>
                    <i title="订单分包" class="primary iconfont icon-subcontractor" @click="subcontractor(scope.row.orderCode)"></i>
                </template>
            </el-table-column>
        </el-table>
        <!--分页-->
    
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }
                }
            },
            searchForm: {
                orderStatus:2,
                createTimeBegin: '',
                createTimeEnd: '',
                orderCode: '',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }

            },

            //包裹列表
            loading: false,
            orderList: [],
            total: 0,
        }
    },

    filters: {
        dateFormat(date) {
            return format('yyyy-MM-dd hh:mm', new Date(date));
        },
        status(data) {
            switch (data) {
                //  0:待支付 1:已取消 2:待发货 3:已打包 4:已发货 5：已签收
                case 0:
                    return '待支付';
                    break;
                case 1:
                    return '已取消';
                    break;
                case 2:
                    return '待发货';
                    break;
                case 3:
                    return '已打包';
                    break;
                case 4:
                    return '已发货';
                    break;
                case 5:
                    return '已签收';
                    break;
                default:
                    return ''
                    break;
            }
        }
    },
    methods: {
        //获取包裹列表
        getorderList() {
            this.loading = true;
            if (this.searchForm.createTimeBegin != '') {
                this.searchForm.createTimeBegin = format('yyyy-MM-dd', new Date(this.searchForm.createTimeBegin));
            }
            if (this.searchForm.createTimeEnd != '') {
                this.searchForm.createTimeEnd = format('yyyy-MM-dd', new Date(this.searchForm.createTimeEnd));
            }
            this.axios.post(this.myConfig.code.proxy + '/ord/order/getOrders', { body: this.searchForm }).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.orderList = data.body.orders;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getorderList();
        },
        search_smt() {
            this.getorderList();
        },
        doAction(id) {
            this.$router.push('/logistics/package-order/' + id);
        },
        subcontractor(id) {
            // supplier/order.php?act=parcel&order_id=50
            localStorage.setItem('hrf', JSON.stringify({ uri: '/fv/supplier/order.php?act=parcel&order_id=' + id, time: new Date().getTime() }))
            this.$router.push({ path: '/main/iframe', query: { tm: new Date().getTime() } });
            this.$route.push('/main/iframe/')
        }
    },
    created() {
        this.getorderList()
    }

}
</script>




