// 物流模块
import SysMain from '@/components/sys/main.vue';
import packageList from '@/components/sys/logistics/package-list.vue';
import packageDetail from '@/components/sys/logistics/package-detail.vue';
import packageManage from '@/components/sys/logistics/package-manage.vue';
import orderDetail from '@/components/sys/logistics/order-detail.vue';

//包裹列表
let router_packageList = {
    path: 'package-list',
    name: 'logistics-package-list',
    component: packageList
}
//包裹详情
let router_packageDetail = {
    path: 'package-detail/:id',
    name: 'logistics-package-detail',
    component: packageDetail
}
//带分包管理
let router_packageManage = {
    path: 'package-manage',
    name: 'logistics-package-manage',
    component: packageManage
}
//订单详情
let router_orderDetail = {
    path: 'package-order/:id',
    name: 'logistics-package-order',
    component: orderDetail
}

let router_logistics = {
    path: 'logistics',
    name: 'logistics',
    component: SysMain,
    children: [router_packageList,router_packageDetail,router_packageManage,router_orderDetail]
}

export default router_logistics;


