// 物流模块
import SysMain from '@/components/sys/main.vue';

const cpaList = r => require.ensure([], () => r(require('@/components/sys/ec/cpa-list.vue')), 'cpa');
const cpaGoods = r => require.ensure([], () => r(require('@/components/sys/ec/cpa-goods.vue')), 'cpa');
const cpaGoodsAdd = r => require.ensure([], () => r(require('@/components/sys/ec/cpa-goods-add.vue')), 'cpa');
const CpaLogistics = r => require.ensure([], () => r(require('@/components/sys/ec/cpa-logistics.vue')), 'cpa');
const CommissionRate = r => require.ensure([], () => r(require('@/components/sys/ec/commission-rate.vue')), 'cpa');
const EcConfig = r => require.ensure([], () => r(require('@/components/sys/ec/ec-config.vue')), 'cpa');

//包裹列表
let router_cpaList = {
    path: 'cpa-list',
    name: 'ec-cpa-list',
    component: cpaList
}

//产品列表
let router_cpaGoods = {
    path: 'cpa-goods/:id',
    name: 'ec-cpa-goods',
    component: cpaGoods
}

//添加产品
let router_cpaGoodsAdd = {
    path: 'cpa-goods-add/:id',
    name: 'ec-cpa-goods-add',
    component: cpaGoodsAdd
}
//cpa 物流区域对应表
let router_cpaLogistics = {
    path: 'cpa-logistics',
    name: 'cpa-logistics',
    component: CpaLogistics
}
//佣金比例折扣
let router_commissionRate = {
    path: 'commission-rate',
    name: 'ec-commission-rate',
    component: CommissionRate
}
// 配置
let router_EcConfig = {
    path: 'cpa-config',
    name: 'ec-cpa-config',
    component: EcConfig
}

let router_ec = {
    path: 'ec',
    name: 'ec',
    component: SysMain,
    children: [router_cpaList, router_cpaGoods, router_cpaGoodsAdd,router_cpaLogistics,router_commissionRate,router_EcConfig]
}

export default router_ec;


