<template>
    <div class="wrap-action">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>翻译管理</el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: getRoute }">{{type|title}}</el-breadcrumb-item>
                <el-breadcrumb-item>翻译</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="main">
            <div class="item">
                <span class="title">产品编号:</span>{{goodsSn}}
            </div>
            <div class="item">
                <span class="title">商品图片:</span><img :src="productImg" alt="商品封面图" @click="showProductImg">
            </div>
            <el-dialog title=" " :visible.sync="dialogImg" :custom-class="'dialog-showimg'" size="tiny">
                <img :src="productImg" alt="商品封面图" @click="showProductImg">
            </el-dialog>
            <el-row class="ueditors ">
                <el-col :span="12">
                    <div class="editor-l">
                        <h4 class="title">原文</h4>
                        <el-form ref="form_original" :model="form_original" label-width="100px" width="95%" class="editor-form">
                            <el-form-item label="标题">
                                <el-input type="textarea" v-model="form_original.title" :readonly="true" :maxlength="150"></el-input>
                            </el-form-item>
                            <el-form-item label="短标题">
                                <el-input type="textarea" v-model="form_original.shortTitle" :readonly="true" :maxlength="50"></el-input>
                            </el-form-item>
                            <el-form-item label="包装信息">
                                <el-input type="textarea" v-model="form_original.packageInfo" :readonly="true"></el-input>
                            </el-form-item>
                            <el-form-item label="简单描述" >
                                <el-input type="textarea" v-model="form_original.goodsBrief" :readonly="true" :maxlength="250"></el-input>
                            </el-form-item>
                            <el-form-item label="描述">
                                <vue-html5-editor :content="form_original.description" :height="500" :auto-height="false"></vue-html5-editor>
                            </el-form-item>
                            <el-form-item :label="item.name" v-for="(item,key) in form_original.extraInfos" :key="item.key">
                                <el-input v-model="item.value" type="textarea" :readonly="true"></el-input>
                            </el-form-item>
                            <el-form-item :label="item.productSn" v-for="(item,key) in form_original.products" :key="item.productId">
                                <el-input v-model="item.productTitle" type="textarea" :readonly="true"></el-input>
                            </el-form-item>
                        </el-form>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="editor-r">
                        <h4 class="title">译文</h4>
                        <el-form ref="form_translate" :model="form_translate" label-width="100px" width="95%" class="editor-form">
                            <el-form-item label="标题" prop="title" :rules="{required:true,message:'标题不能为空'}">
                                <el-input type="textarea" v-model="form_translate.title" :maxlength="150"></el-input>
                            </el-form-item>
                            <el-form-item label="短标题" prop="shortTitle" :rules="{required:true,message:'短标题不能为空'}">
                                <el-input type="textarea" v-model="form_translate.shortTitle" :maxlength="50"></el-input>
                            </el-form-item>
                            <el-form-item label="包装信息" prop="packageInfo" :rules="{required:true,message:'包装信息不能为空'}">
                                <el-input type="textarea" v-model="form_translate.packageInfo"></el-input>
                            </el-form-item>
                            <el-form-item label="简单描述" prop="goodsBrief"  :rules="{required:true,message:'商品简单描述'}">
                                <el-input type="textarea" v-model="form_translate.goodsBrief" :maxlength="250"></el-input>
                            </el-form-item>
                            <el-form-item label="描述" class="input-description" ref="decription">
                                <vue-html5-editor :content="form_translate.description" :height="500" :auto-height="false" @change="onEditorChange"></vue-html5-editor>
                                <div class="el-form-item__error" v-if="discription_error">描述不能为空</div>
                            </el-form-item>
                            <el-form-item :label="item.name" v-for="(item,key) in form_translate.extraInfos" :key="item.key" :prop="'extraInfos.' + key + '.value'" :rules="{required: true, message: item.name+'不能为空'}">
                                <el-input v-model="item.value" type="textarea"></el-input>
                            </el-form-item>
                            <el-form-item :label="item.productSn" v-for="(item,key) in form_translate.products" :key="item.productId" :prop="'products.' + key + '.productTitle'" :rules="{required: true, message: item.productSn+'不能为空'}">
                                <el-input v-model="item.productTitle" type="textarea"></el-input>
                            </el-form-item>
                        </el-form>

                    </div>

                </el-col>
            </el-row>
            <div class="content-count">
                <span class="title">文字统计：</span>
                <span>
                    总计： {{(textCount)}}
                </span>
                <span>标题:
                    <strong>{{form_translate.title|trimLen}}</strong>
                </span>
                <span>短标题:
                    <strong>{{form_translate.shortTitle|trimLen}}</strong>
                </span>
                <span>包装信息:
                    <strong>{{form_translate.packageInfo|trimLen}}</strong>
                </span>
                <span v-if="form_translate.goodsBrief">商品简单描述:
                    <strong>{{form_translate.goodsBrief|trimLen}}</strong>
                </span>
                <span>描述:
                    <strong>{{discription_len}}</strong>
                </span>
                <span v-for="(item,key) in form_translate.extraInfos" :key="item.key">{{item.name}}:
                    <strong>{{item.value|trimLen}}</strong>
                </span>
                <span v-for="(item,key) in form_translate.products" :key="item.productId">{{item.productSn}}:
                    <strong>{{item.productTitle|trimLen}}</strong>
                </span>
                <span></span>
            </div>
            <div class="remark" v-if="remark">
                备注信息：
                <span class="tip">{{remark}}</span>
            </div>
            <div class="submit">
                <el-button type="primary" @click="doTranslate('')" v-if="(status==2 || status==5) && type!='auto'">保存草稿</el-button>
                <el-button type="primary" @click="doTranslate(3)" v-if="status==2 || status==5">提交审核</el-button>
                <el-button type="primary" @click="doTranslate(3,1)" v-if="status==2 || status==5">提交并开始下一个</el-button>
                <el-button type="primary" @click="doTranslate(4)" v-if="status==3 ">审核通过</el-button>
                <el-button type="primary" @click="doTranslate(4)" v-if="status==4 ">保存</el-button>
                <el-button type="primary" @click="changeStatus(5)" v-if="status==3 && type!='auto' ">审核不通过</el-button>
                <el-button type="primary" @click="changeStatus(6)" v-if="((status==2 || status==5)&& type!='auto') ||(status==3 && type=='auto' )">驳回</el-button>
                <el-button type="primary" @click="$router.push(getRoute)">返回</el-button>
            </div>

            <!-- 备注信息 -->
            <el-dialog title="备注信息" :visible.sync="dialogRemarkVisible" size="tiny" :close-on-click-modal="false">
                <el-form :model="remarkForm" ref="remarkForm" label-width="100px">
                    <el-form-item label="备注" prop="remark" :rules="{required: true, message: '备注信息不能为空'}">
                        <el-input v-model="remarkForm.remark" auto-complete="off" type="textarea"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogRemarkVisible = false">取 消</el-button>
                    <el-button type="primary" @click="submit_remark">确 定</el-button>
                </div>
            </el-dialog>
        </div>

    </div>
</template>

<script>
export default {
    data() {
        return {
            discription_len: 0,
            //用户类型
            type: '',
            //产品编号
            goodsSn: '',
            dialogRemarkVisible: false,
            remarkForm: { remark: '' },
            taskId: 1,
            status: 1,
            changeMyStatus: '',
            remark: '',
            form_original: {
                description: "",
                packageInfo: "",
                shortTitle: "",
                goodsBrief: "",
                title: "",
                extraInfos: []
            },
            form_translate: {
                description: "",
                description_txt: 0,//富文本的字数
                packageInfo: "",
                shortTitle: "",
                title: "",
                goodsBrief: "",
                extraInfos: []
            },

            discription_error: false,
            initEditorData: {},
            textCount: 0,//除了富文本的字数,
            //产品图片
            productImg:'',
            dialogImg:false,
        }
    },
    computed: {
        getRoute(status) {
            switch (this.type) {
                case 'op':
                    return '/translate/task/op';
                    break;
                case 'ad':
                    return '/translate/task/ad';
                    break;
                case 'mytask':
                    return '/translate/myTask';
                    break;
                case 'allot':
                    return '/translate/allotTask';
                    break;
                case 'auto':
                    if (this.$route.query.frm) {
                        return '/translate/task/' + this.$route.query.frm;
                    } else {
                        return '/translate/auto';
                    }
                    break;

            }
        }
    },
    filters: {
        trimLen(data) {
            return data.replace(/\s/g, '').length
        },
        title(data) {
            switch (data) {
                case 'op':
                    return '翻译任务管理';
                    break;
                case 'ad':
                    return '翻译任务管理';
                    break;
                case 'mytask':
                    return '我的任务管理';
                    break;
                case 'allot':
                    return '翻译任务领取';
                    break;
                case 'auto':
                    return '机器翻译';
                    break;
            }
        }
    },
    methods: {
        init() {
            //获取id
            this.type = this.$route.params.type;
            this.taskId = this.$route.params.id;
            this.status = this.$route.params.status;
            this.axios.post(this.myConfig.code.proxy + '/translate/api/task/detail', { body: { taskId: this.taskId } }).then(res => {
                let data = res.data;
                if (data.head.code == "10000") {
                    this.productImg=data.body.goodsImgUrl;
                    this.form_original = data.body.taskData;
                    this.remark = data.body.remark;
                    this.goodsSn = data.body.goodsSn;
                    if (data.body.translateData) {
                        this.form_translate = data.body.translateData;
                    } else {
                        this.form_translate = JSON.parse(JSON.stringify(this.form_original));
                        this.form_translate.description = '';
                        this.form_translate.packageInfo = '';
                        this.form_translate.shortTitle = '';
                        this.form_translate.title = '';
                        this.form_translate.goodsBrief = '';
                        if (this.form_translate.products) {
                            for (let i in this.form_translate.products) {
                                this.form_translate.products[i].productTitle = '';
                            }
                        }
                        if (this.form_translate.extraInfos) {
                            for (let i in this.form_translate.extraInfos) {
                                this.form_translate.extraInfos[i].value = '';
                            }
                        }
                    }
                    this.discription_len = this.form_translate.description.replace(/<[^>]+>/g, '').replace(/&nbsp;/g, '').replace(/\s/g, '').length;
                   
                }

            })
        },
        //翻译
        doTranslate(val, isEnterNext) {
            let that = this;
            if (this.form_translate.description.trim() == '') {
                this.discription_error = true;

            } else {
                this.discription_error = false;
            }

            function getCount() {
                let count = 0;
                count += that.form_translate.title.replace(/\s/g, '').length;
                count += that.form_translate.shortTitle.replace(/\s/g, '').length;
                count += that.form_translate.packageInfo.replace(/\s/g, '').length;
                if (that.form_translate.goodsBrief) {
                    count += that.form_translate.goodsBrief.replace(/\s/g, '').length;
                }
                count += that.discription_len;
                if (that.form_translate.extraInfos) {
                    for (let i in that.form_translate.extraInfos) {
                        count += that.form_translate.extraInfos[i].value.replace(/\s/g, '').length;
                    }
                }
                if (that.form_translate.products) {
                    for (let i in that.form_translate.products) {
                        count += that.form_translate.products[i].productTitle.replace(/\s/g, '').length;
                    }
                }
                return count;
            }

            if (val == '') {//保存草稿
                that.axios.post(this.myConfig.code.proxy + '/translate/api/detail/save', { body: { totalChar: getCount(), taskId: this.taskId, taskStatus: val, translateData: this.form_translate } }).then((res) => {
                    if (res.data.head.code == '10000') {
                        that.$router.push(this.getRoute);
                    }
                })
            } else {//保存
                this.$refs.form_translate.validate((validate) => {
                    if (validate && this.form_translate.description.trim() != '') {
                        that.axios.post(this.myConfig.code.proxy + '/translate/api/detail/save', { body: { isEnterNext: isEnterNext ? 1 : 0, totalChar: getCount(), taskId: this.taskId, taskStatus: val, translateData: this.form_translate } }).then((res) => {
                            if (res.data.head.code == '10000') {
                                if (!res.data.body) {
                                    that.$router.push(this.getRoute);
                                } else {
                                    that.$store.dispatch('setScrollTop', 0)
                                    that.$router.push('/translate/action/' + res.data.body.taskId + '/' + res.data.body.taskStatus + '/' + this.type);
                                    that.init();
                                }


                            }
                        })
                    }

                })
            }

        },
        //改变状态
        changeStatus(status) {
            this.remarkForm = { remark: '' };
            if (status == 5 || status == 6) {
                this.changeMyStatus = status;
                this.dialogRemarkVisible = true;
            } else {
                this.axios.post(this.myConfig.code.proxy + '/translate/api/change/status', { body: { taskStatus: status, taskIds: [this.taskId] } }).then((res) => {
                    if (res.data.head.code == '10000') {
                        this.$router.push(this.getRoute);

                    }
                })
            }
        },
        //提交备注，
        submit_remark() {
            this.$refs.remarkForm.validate((validate) => {
                if (validate) {
                    this.axios.post(this.myConfig.code.proxy + '/translate/api/change/status', { body: { taskStatus: this.changeMyStatus, taskIds: [this.taskId], remark: this.remarkForm.remark } }).then((res) => {
                        if (res.data.head.code == '10000') {
                            this.dialogRemarkVisible = false;
                            this.$router.push(this.getRoute);
                        }
                    })
                }
            })

        },
        getTotoalCount(val) {

        },

        //监听富文本change
        onEditorChange(_data) {
            let that = this;
            let data = _data;
            data = data.replace(/<[^>]+>/g, '').replace(/&nbsp;/g, '').replace(/\s/g, '');
            console.log(data,444444444)
            this.discription_len = data.length;
            this.form_translate.description = _data;
            function getCount() {
                let count = 0;
                count += that.form_translate.title.replace(/\s/g, '').length;
                count += that.form_translate.shortTitle.replace(/\s/g, '').length;
                count += that.form_translate.packageInfo.replace(/\s/g, '').length;
                if (that.form_translate.goodsBrief) {
                    count += that.form_translate.goodsBrief.replace(/\s/g, '').length;
                }
                count += that.discription_len;
                if (that.form_translate.extraInfos) {
                    for (let i in that.form_translate.extraInfos) {
                        count += that.form_translate.extraInfos[i].value.replace(/\s/g, '').length;
                    }
                }
                if (that.form_translate.products) {
                    for (let i in that.form_translate.products) {
                        count += that.form_translate.products[i].productTitle.replace(/\s/g, '').length;
                    }
                }
                return count;
            }
            this.textCount = getCount();
        },


        //展示商品封面图
        showProductImg(){
            this.dialogImg=true;
        }

    },
    mounted() {
        this.init();
    },
    watch: {
        form_translate: {
            // handler: 'getTotoalCount',
            handler(val, oldVal) {
                let _this = this;
                function getCount() {
                    let count = 0;
                    count += val.title.replace(/\s/g, '').length;
                    count += val.shortTitle.replace(/\s/g, '').length;
                    count += val.packageInfo.replace(/\s/g, '').length;
                    if (val.goodsBrief) {
                        count += val.goodsBrief.replace(/\s/g, '').length;
                    }
                    count += _this.discription_len;
                    if (val.extraInfos) {
                        for (let i in val.extraInfos) {
                            count += val.extraInfos[i].value.replace(/\s/g, '').length;
                        }
                    }
                    if (val.products) {
                        for (let i in val.products) {
                            count += val.products[i].productTitle.replace(/\s/g, '').length;
                        }
                    }
                    return count;
                }
                this.textCount = getCount();
            },
            deep: true
        }
    }
}
</script>


<style lang="less" scroped>
.wrap-action {
    padding-right: 40px;
    .editor-l {
        padding-right: 10px;
    }
    .editor-r {
        padding-left: 10px;
    }
}

.editor-form {
    width: 98%;
}


.ql-container {
    height: 400px;
    overflow-x: auto;
}

.submit {
    padding: 10px;
    text-align: center
}

.input-description .el-form-item__label::before {
    content: '*';
    color: #ff4949;
    margin-right: 4px;
}

.el-form-item__label {
    word-wrap: break-word;
}

.remark {
    padding: 15px;
    .tip {
        color: #ff4949;
    }
}

.item {
    padding: 10px 0;
    .title {
        font-weight: 700;
        margin-right: 10px;
        display:  inline-block;
        vertical-align: middle;
    }
    img{
        width: 60px;
        display:  inline-block;
        vertical-align: middle;
        cursor: pointer;
    }
}

.content-count {
    padding: 10px 0;
    span {
        margin-right: 10px;
    }
}
</style>
<style lang="less">
.notify{
    z-index: 99999;
}
.dialog-showimg{
    width:385px;
    img{
        width:350px;
        height:350px;
        border-radius:4px;
    }

}

</style>





