<template>
  <div class="iframe" v-loading="loading">
      
    <iframe :src="url" width="100%" height="100%" @load="loaded" id="vendor-iframe" ></iframe>
  </div>
</template>

<style lang="less" scoped>
.iframe {
  height: 100%;
}
</style>

<script>
export default {
  data() {
    return {
      url: '',
      loading:true,
      
    }
  },
  computed:{
    
  },
  methods: {
    loaded(){
      this.loading=false;
    },
    
    
  },
  created() {
      
      this.url='/proxy'+decodeURIComponent(this.$route.query.url);
      console.log(this.$route,this.url)
  },
  
}
</script>



