        <el-dialog title="修改密码" :visible.sync="dialogModifyPwd" :close-on-click-modal="false" size="tiny" :before-close="modifyPwd_close">
            <el-form :model="formPwd" class="modify-pwd" ref="modifyPwd" label-width="100px" :rules="modifyPwdRules">
                <el-form-item label="初始密码" prop="oldPassword">
                    <el-input v-model="formPwd.oldPassword" type="password"></el-input>
                </el-form-item>
                <el-form-item label="新密码" prop="newPassword">
                    <el-input v-model="formPwd.newPassword" type="password"></el-input>
                </el-form-item>
                <el-form-item label="确认新密码" prop="newPasswordCheck">
                    <el-input v-model="formPwd.newPasswordCheck" type="password"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogModifyPwd = false">取 消</el-button>
                <el-button type="primary" @click="smt_modifyPwd">确 定</el-button>
            </div>
        </el-dialog>        
        <div class="head-flex-box">
            <div class="flex-logo">
                <img src="../assets/common/images/logo.png" alt=""> 
                <span >丰云综合平台</span>
                
            </div>
            <div class="flex-menu">
                <el-menu :default-active="headMenu" class="el-menu-demo" mode="horizontal" @select='menuSelect' background-color="#000" text-color="#fff"  active-text-color="#fff">
                    <el-menu-item :index="val.menuId" v-for="(val,key) in authority.menus" :key="key">
                        <div class="item-box">
                            <i :class="'iconfont icon-'+val.menuIco"></i>
                            <span class="txt" >{{val.menuName}}</span>
                        </div>
                    </el-menu-item>
                </el-menu>                
            </div>
            <div class="flex-userinfo">
                <span class="pl-txt">
                    Hi,{{login?login.username:''}} |
                </span>
                <el-dropdown trigger="click" class="pl-action">
                    <span class="el-dropdown-link">
                        <i class="iconfont icon-xitongguanli"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown" class="el-dropdown-loginout">
                        <el-dropdown-item @click.native="modifyPwd" class="modifyPwd">修改密码</el-dropdown-item>
                        <el-dropdown-item @click.native="logout">退出</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
    
    </div>
