<style lang="less" scoped>
.wrap-header{
    color:#fff;
    .head-flex-box{
        display: flex;
        .flex-logo{
            width: 240px;
            display: flex;
            justify-content: center;
            position: relative;           
            img{
                width: 40px;
                height: 21px;
                margin-right: 10px;
            }
            align-items: center;
            font-size: 20px;
            font-weight: bolder;
        }
        .flex-menu{
            flex: 1;
            height: 70px;
            padding-left: 50px;
            .el-menu--horizontal{
                border:none;
            }
            .el-menu-item{
                line-height: normal;
                height: 70px;
                border: none;
                .item-box{
                    display: inline-block;
                    padding: 2px 10px;
                    width: auto;
                    height: auto;
                    text-align: center;
                    border: 1px solid #000;
                    margin-top: 12px;
                    .txt{
                        display: block;
                        color:#909399;
                    }
                }
                &.is-active{
                    .item-box{
                        border-color:#fff;
                        .txt{
                            color:#fff;
                        }
                    }
                }
            }
        }
        .flex-userinfo{
            width:400px; 
            display: flex;
            align-items: center;
            justify-content: flex-end;
            .pl-text{
                flex:1;
            }
            .pl-action{
                width: 100px;
                    margin-top: 4px;
                    margin-left: 10px;
                    color: #fff;
            }

        }
    }
}

</style>


<style lang="less">



.el-dialog__header{
text-align:left;
}
.el-dropdown-loginout {
    border:0;
    padding: 0;
    margin-left:-50px;
    .el-dropdown-menu__item {
        font-size: 10px;
        height: 28px;
        line-height: 28px;
        &:hover{
            background:#CA1A1A;
            color:#fff;
        }
    }
    .modifyPwd {
        .iconfont {
            color: #d73435;
        }
    }
}
</style>


<script>
import { mapGetters } from 'vuex'

export default {
    data() {
        let checkPwd = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('请再次输入密码'));
            }
            setTimeout(() => {
                if (value !== this.formPwd.newPassword) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            }, 500);
        };
        return {
            //登入信息
            login: {},
            //修改密码
            formPwd: {
                oldPassword: '',
                newPassword: "",
                newPasswordCheck: ''
            },
            dialogModifyPwd: false,
            modifyPwdRules: {
                oldPassword: [{ required: true, message: '密码不能为空' }],
                newPassword: [{ required: true, message: '密码不能为空' }],
                newPasswordCheck: [
                    { required: true, message: '密码不能为空' },
                    { validator: checkPwd },
                ]
            },
            headMenu: ''
        }

    },
    computed: {
        ...mapGetters({
            authority: 'getAuthority',
            collapse:'getCollapse'
        })
    },
    methods: {
        logout() {
            let that = this;

            //退出请求
            this.axios.post(this.myConfig.code.auth + '/api/logout', {}).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {//退出成功

                    //删除cookie
                    // that.$cookie.delete('token', { path: "/", domain: that.myConfig.domain });//开发
                    that.$cookie.delete('token', { path: "/" });//生产
                    window.localStorage.removeItem('login');
                    window.localStorage.removeItem('act');
                    window.localStorage.removeItem('setAuthSelect');
                    window.localStorage.removeItem('userInfo');
                    window.localStorage.removeItem('headMenu');
                    window.localStorage.removeItem('hrf');
                    that.$message({
                        type: 'success',
                        message: '退出成功',
                        duration: 1000,
                        onClose: function () {
                            that.$router.push('/login')
                        }
                    })
                } else {
                    that.$message({
                        type: 'error',
                        message: '退出失败',
                        duration: 1000
                    })
                }

            }).catch(error => {
                that.$message({
                    type: 'error',
                    message: '网络错误',
                    duration: 1000
                })
            })
        },
        //修改密码
        modifyPwd() {
            this.dialogModifyPwd = true;
        },
        modifyPwd_close(done) {
            done();
            this.formPwd = {
                oldPassword: '',
                newPassword: "",
                newPasswordCheck: ''
            }
        },
        //提交修改密码表单
        smt_modifyPwd() {
            // this.dialogModifyPwd=false;
            this.$refs.modifyPwd.validate((validate) => {
                if (validate) {
                    this.axios.post(this.myConfig.code.auth + '/api/modifyPwd', { body: this.formPwd }).then(response => {
                        if (response.data.head.code === '10000') {
                            this.$message({
                                type: "success",
                                message: '修改成功',
                                duration: 1000
                            })
                            this.dialogModifyPwd = false;
                        } else {
                            this.$message({
                                type: "success",
                                message: response.data.body.sub_msg,
                                duration: 1000
                            })
                        }
                    })
                }
            })
        },
        menuSelect(key) {
            for (let i in this.authority.menus) {
                if (this.authority.menus[i].menuId == key) {
                    localStorage.setItem('headMenu', key);
                    localStorage.setItem('setAuthSelect', JSON.stringify(this.authority.menus[i]));
                    this.$store.dispatch('setAuthSelect', this.authority.menus[i]);  //一级菜单列表;
                }
            }

        }
    },
    created() {
        this.login = JSON.parse(localStorage.getItem('login'));
        this.headMenu = window.localStorage.getItem('headMenu');
    },
    mounted() {

    }

}
</script>





