//订单模块

//main sys components
import SysMain from '@/components/sys/main.vue'
//main欢迎页面
import MainWelcome from '@/components/sys/Welcome.vue'


const refundList = r => require.ensure([], () => r(require('@/components/sys/order/refund-list.vue')), 'goodsOrder');




//首页
let router_index = {
    path: 'index',
    name: 'order-index',
    component: MainWelcome
}

//商品物流转换
let router_refundList = {
    path: 'refund-list',
    name: 'order-refund-list',
    component: refundList
}



let router_vendor = {
    path: 'order',
    name: 'order',
    redirect: '/order/index',
    component: SysMain,
    children: [router_index, router_refundList]
}
export default router_vendor


