<template>
    <div id="app">
        <router-view></router-view>
    </div>
</template>

<script>
export default {
  name: "app"
};
</script>

<style lang="less">
html,
body {
  margin: 0;
  padding: 0;
  font-family: "Microsoft Yahei", "微软雅黑", Tahoma, Arial, Helvetica, STHeiti;
}

html {
  width: 100%;
  height: 100%;
}

body {
  height: 100%;
  width: 100%;
}

textarea {
  font-family: "Microsoft YaHei" !important;
}

#app {
  height: inherit;
  width: 100%;
}

.main-wrap {
  padding-right: 40px;
}

/* //全局样式 */

.iconfont {
  &.primary {
    color: #3c96d6;
    margin-right: 8px;
    cursor: pointer;
    font-size: 18px !important;
    &:hover,
    &::active {
      color: #2077b4;
    }
  }
  &.danger {
    margin-right: 8px;
    color: #e05c5c;
    cursor: pointer;
    font-size: 18px !important;
    &:hover,
    &::active {
      color: #c94646;
    }
  }
}

.el-table {
  color: #696969;
  th {
    background: #fafafa;
    .cell {
      background: #fafafa;
    }
  }

  th,td {
    height: auto;
    padding: 10px 0;
  }
  .cell {
    line-height: 20px;
    font-size: 14px;
  }
  
}
.el-table th>.cell{
    line-height: normal;
  }
.el-table .cell, .el-table th > div{
  line-height: normal;
}
.report,
.el-table__body-wrapper,
.el-table__fixed-body-wrapper {
  .cell {
    font-size: 12px;
  }
  th,td {
    padding: 6px 0;
  }
}
.table-border {
  border-left-width: 1px;

  td {
    border-width: 1px;
  }
}
.el-table .cell,
.el-table th > div {
  padding: 0 8px;
}

// 动画
.slide-fade-enter-active {
  transition: all 0.6s;
  transition-delay: 0.2s;
}

.slide-fade-leave-active {
  transition: all 0.6s;
}

.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
}

.animation {
  transition: width 0.6s;
}

//report notify
.notify-report {
  .el-notification__title {
    font-weight: bold;
  }
  .el-notification__content {
    div {
      max-height: 350px;
      overflow-y: auto;
    }
    p {
      margin: 0;
      color: #222;
      font-size: 12px;
    }
  }
} //导入
.import {
  padding: 0 0 6px 0;
}
.doAction{
  margin-bottom:6px;
}
.el-message {
  top: 30%;
}

.el-autocomplete,
.el-dropdown {
  width: 100%;
}

//查询按钮
.search {
  background-color: #f7f7f7;
  border-radius: 8px;
  padding: 10px 0 0 0px;
  margin-bottom: 15px;

  .el-icon-search {
    padding-top: 10px;
  }

  .el-form--inline .el-form-item {
    .el-input__inner {
      height: 30px;
      line-height: 30px;
    }
    margin-right: 15px;
    margin-bottom: 10px;
    &.arrow {
      margin-left: -15px;
      .iconfont {
        font-size: 20px;
      }
    }
  }
  .el-button {
    .el-icon-search {
      padding-top: 0px;
    }
    padding: 7px 15px;
    margin-left: 15px;
    .iconfont {
      margin-right: 4px;
      font-size: 12px;
    }
  }
  
  .el-form-item__content {
    width: 230px;
    .el-select{
      width: 100%;
    }
  }
  .search-btn{
    .el-form-item__content{
      width: auto;
    }
    
  }
  .el-date-editor--daterange.el-input {
    width: 230px;
  }
  .fold {
    text-align: right;
    cursor: pointer;
    font-size: 14px;
    padding-right: 15px;
    padding-bottom: 8px;
    .iconfont {
      margin-left: 5px;
      &.icon-fold-open {
        color: #ea2000;
      }
    }
  } //报表样式
  &.report {
    .el-form-item__content {
      // line-height: 30px;
    }
    .el-form--inline .el-form-item {
      margin-bottom: 0px;
      margin-right: 8px;
    }
    .el-form-item__label {
      font-size: 12px;
    }
    .el-input__inner {
      font-size: 12px;
    }
    .el-form--inline .el-form-item .el-input__inner {
      height: 28px;
      line-height: 28px;
    }
    .el-button {
      padding: 6px 10px;
      font-size: 12px;
      margin-left: 30px;
    }
  }
}
</style>
<script>
export default {
  name: "app"
};
</script>




