// 公共页面
import SysMain from '@/components/sys/main.vue'
//main欢迎页面
import MainWelcome from '@/components/sys/Welcome.vue'
// ifram 页面 外连接页面
import Iframe from '@/components/sys/Iframe.vue';

import Panel from '@/components/sys/main/ThePanel.vue';
import Jump from '@/components/sys/main/TheJumping.vue';

//欢迎页面
let router_index = {
    path: 'index',
    name: 'main-index',
    component: MainWelcome
}

// ifram 页面 外连接页面
let router_iframe = {
    path: 'iframe',
    name: 'main-iframe',
    component: Iframe
}
// 控制面板
let router_panel = {
    path: 'panel',
    name: 'main-panel',
    component: Panel
}
// 控制面板
let router_jump = {
    path: 'Jump',
    name: 'main-jump',
    component: Jump
}

let router_main = {
    path: 'main',
    name: 'main',
    redirect: '/main/panel',
    component: SysMain,
    children: [router_index, router_iframe, router_panel, router_jump]
}

export default router_main;


