<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>物流管理</el-breadcrumb-item>
                <el-breadcrumb-item>包裹单列表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
    
        <!-- 搜索 -->
        <div class="search">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline">
                <i class="el-icon-search"></i>
                <el-form-item label="订单号">
                    <el-input v-model="searchForm.orderCode" placeholder="订单号" size="small"></el-input>
                </el-form-item>
                <el-form-item label="包裹单号">
                    <el-input v-model="searchForm.packageCode" placeholder="包裹单号" size="small"></el-input>
                </el-form-item>
                <el-form-item label="运单号">
                    <el-input v-model="searchForm.waybillNo" placeholder="运单号" size="small"></el-input>
                </el-form-item>
                <el-form-item label="物流类型">
                    <el-select v-model="searchForm.logisticsCode" placeholder="物流类型" size="small">
                        <el-option label="所有" value=""></el-option>
                        <el-option :label="item.logisticsName" :value="item.logisticsCode" v-for="(item,key) in logisticsTypes" :key="item.logisticsCode"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt" size="">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--包裹列表-->
        <el-table :data="packageList" stripe style="width: 100%" v-loading="loading">
            <el-table-column label="包裹单号" prop="packageCode" align="center" width="180"></el-table-column>
            <el-table-column label="订单号" prop="orderCode" align="center" width="180"> </el-table-column>
            <el-table-column label="运单号" prop="waybillNo" align="center" width="150"> </el-table-column>
            <el-table-column label="代理运单号" prop="agentWaybillNo" align="center"  width="150"> </el-table-column>
            <el-table-column label="物流类型" align="center" width="100">
                <template slot-scope="scope">{{scope.row.logisticsName}}</template>
            </el-table-column>
            <el-table-column label="是否签收" align="center" width="100">
                <template slot-scope="scope">{{scope.row.isSigned==0?'否':'是' }}</template>
            </el-table-column>
            <el-table-column label="发货仓库" prop="warehouseName" align="center" min-width="100"> </el-table-column>
            <el-table-column label="创建人" prop="createUserName" align="center"> </el-table-column>
            <el-table-column label="创建时间" align="center" width="160">
                <template slot-scope="scope">{{scope.row.createTime|dateFormat}}</template>
            </el-table-column>
            <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                    <i title="查看" class="primary iconfont icon-chakan" @click="doAction(scope.row.packageCode)"></i>
                   <!-- http://localhost:81/logistics/print?waybillNo=070034529829&logisticsCode=12000&merchantCode=020S00001 -->
                    <a :href="'/proxy/logistics/logistics/print?waybillNo='+scope.row.waybillNo+'&merchantCode='+scope.row.merchantCode+'&logisticsCode='+scope.row.logisticsCode"  download><i title="打印" class="primary iconfont icon-dayin" ></i></a>
                </template>
            </el-table-column>
        </el-table>
        <!--分页-->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    
    </div>
</template>

<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            searchForm: {
                orderCode: '',
                packageCode: '',
                waybillNo: '',
                logisticsCode: '',
                page: 1,
                pageSize: this.myConfig.pageRows
            },

            //物流方式
            logisticsTypes: [],

            //包裹列表
            loading: false,
            packageList: [],
            total: 0,
        }
    },
    filters: {
        dateFormat(date) {
            return format('yyyy-MM-dd hh:mm', new Date(date));
        }
    },
    methods: {
        //获取物流类型
        getLogisticsType() {
            this.axios.post(this.myConfig.code.proxy + '/logistics/logistics/queryType', { body: { languageCode: 'cn' ,"status": 0} }).then((response) => {
                var data = response.data;
                if (data.head.code == '10000') {
                    this.logisticsTypes = data.body.logisticsTypes;
                }
            })
        },
        //搜索
        search_smt() {
            this.searchForm.page = 1;
            this.getPackageList();
        },
        //获取包裹列表
        getPackageList() {
            this.loading=true;
            this.axios.post(this.myConfig.code.proxy + '/logistics/package/query', { body: { queryReq: this.searchForm } }).then(response => {
                this.loading=false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.packageList = data.body.packages;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page = val;
            this.getPackageList();
        },
        //查看
        doAction(id) {
            this.$router.push('/logistics/package-detail/'+id);
        },
        print() {

        }

    },
    created() {
        this.getLogisticsType();
        this.getPackageList();
    }
}
</script>



