//商家模块

//main sys components
const SysMain = r => require.ensure([], () => r(require('@/components/sys/main.vue')), 'sys-main');
const MainWelcome = r => require.ensure([], () => r(require('@/components/sys/Welcome.vue')), 'main-welcome');

const Vload_import = r => require.ensure([], () => r(require('@/components/sys/goods/load-import.vue')), 'goods-load-import');
// 运费模板
const VfreightTemplate = r => require.ensure([], () => r(require('@/components/sys/goods/freightTemplate/freight-template.vue')), 'goods-vendor-freight');
const VtemplateList = r => require.ensure([], () => r(require('@/components/sys/goods/freightTemplate/freight-list.vue')), 'vendor-freight-list');


// 商品添加
const GoodsList = r => require.ensure([], () => r(require('@/components/sys/goods/goods-list.vue')), 'vendor-goods-list');
const GoodsCategory = r => require.ensure([], () => r(require('@/components/sys/goods/goods-category.vue')), 'vendor-goods-add');
const GoodsAdd = r => require.ensure([], () => r(require('@/components/sys/goods/goods-add.vue')), 'vendor-goods-add');
const DraftList = r => require.ensure([], () => r(require('@/components/sys/goods/draft-list.vue')), 'vendor-draft-list');
const GoodsShortCut = r => require.ensure([], () => r(require('@/components/sys/goods/shortcut.vue')), 'goods-shortcut');
const UploadHelp = r => require.ensure([], () => r(require('@/components/sys/goods/upload-help.vue')), 'goods-shortcut');

//首页
let router_vendor_index = {
    path: 'index',
    name: 'vendor-index',
    component: MainWelcome
}



//商品模板下载与导入
let router_load_import = {
    path: 'load-import',
    name: 'vendor-load-import',
    component: Vload_import
}




//运费模板
let router_freightTemplate = {
        path: 'freight/:id',
        name: 'vendor-freight-template',
        component: VfreightTemplate,
    }
    //运费模板
let router_freightList = {
        path: 'freight-list',
        name: 'vendor-freight-list',
        component: VtemplateList,

    }
    //运费模板跳转提示
let router_freightTmp = {
    path: 'freight-tmp',
    name: 'vendor-freight-tmp',
    component: VtemplateList,

}
// 商品选择分类
let router_goods_category = {
    path: 'goods-category',
    name: 'vendor-goods-category',
    component: GoodsCategory,

}
// 商品列表
let router_goods_list = {
    path: 'goods-list',
    name: 'vendor-goods-list',
    component: GoodsList,
}
let router_goods_list_audit = {
    path: 'goods-list-audit',
    name: 'vendor-goods-list-audit',
    component: GoodsList,
}
let router_goods_shortcut = {
    path: 'goods-shortcut/:type/:id',
    name: 'vendor-goods-shortcut',
    component: GoodsShortCut,
}
// 商品添加
let router_goods_add = {
    path: 'goods-add/:type',
    name: 'vendor-goods-add',
    component: GoodsAdd,
}
//草稿列表
let router_draft_list= {
    path: 'draft-list',
    name: 'vendor-draft-list',
    component: DraftList,
}
//商品上传帮助页面
let router_upload_help= {
    path: 'upload-help',
    name: 'vendor-upload-help',
    component: UploadHelp,
}
let router_vendor = {
    path: 'vendor',
    name: 'vendor',
    redirect: '/vendor/index',
    component: SysMain,
    children: [router_vendor_index, router_load_import,router_freightTemplate, router_freightList, router_freightTmp,router_goods_add,router_goods_category,router_goods_list,router_draft_list,router_goods_shortcut,router_upload_help,router_goods_list_audit]
}
export default router_vendor


