<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>物流管理</el-breadcrumb-item>
                <el-breadcrumb-item :to="{path:'/logistics/package-manage'}">带分包管理</el-breadcrumb-item>
                <el-breadcrumb-item>带分包单详情</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--包裹详情-->
        <div class="order-info">
            <div class="item">
                <span class="label">订单号:</span>
                <span class="txt">{{orderDetail.orderCode}}</span>
            </div>
            <div class="item">
                <span class="label">订单状态:</span>
                <span class="txt">{{orderDetail.orderStatus}}</span>
            </div>
            <div class="item">
                <span class="label">所属商家:</span>
                <span class="txt">{{orderDetail.merchantCode}}</span>
            </div>
            <div class="item">
                <span class="label">下单时间:</span>
                <span class="txt">{{orderDetail.orderTime|dateFormat}}</span>
            </div>
            <div class="item">
                <span class="label">用户备注:</span>
                <span class="txt">{{orderDetail.note}}</span>
            </div>
    
        </div>
        <el-table :data="orderDetail.orderItems" stripe style="width: 100%">
            <el-table-column label="sku编码" prop="skuCode" align="center" width="180"></el-table-column>
            <el-table-column label="sku名称" prop="skuName" align="center" width="180"> </el-table-column>
            <el-table-column label="sku数量" prop="skuName" align="center" width="150"> </el-table-column>
            <el-table-column label="sku重量(g)" prop="weight" align="center"> </el-table-column>
            <el-table-column label="物流类型" prop="deviveryName" align="center"> </el-table-column>
        </el-table>
    </div>
</template>

<style lang="less" scoped>
.warp {
    padding-right: 40px;
}

.order-info {
    padding-bottom: 20px;
    font-size: 14px;
    width: 60%;
    .item {
        padding: 10px;
        .label {
            display: inline-block;
            width: 100px;
            padding-right: 30px;
            color: #1f2d3d;
            font-weight: bold;
            text-align: right;
        }
    }
}
</style>


<script>
import format from 'date-format';
export default {
    data() {
        return {
            searchForm: {
                orderCode: '',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            orderDetail: {}
        }
    },
    filters: {
        dateFormat(date) {
            return format('yyyy-MM-dd hh:mm', new Date(date));
        },
        status(data) {
            switch (data) {
                //  0:待支付 1:已取消 2:待发货 3:已打包 4:已发货 5：已签收
                case 0:
                    return '待支付';
                    break;
                case 1:
                    return '已取消';
                    break;
                case 2:
                    return '待发货';
                    break;
                case 3:
                    return '已打包';
                    break;
                case 4:
                    return '已发货';
                    break;
                case 5:
                    return '已签收';
                    break;
                default:
                    return ''
                    break;
            }
        }
    },
    methods: {
        getOrderDetail() {
            this.axios.post(this.myConfig.code.proxy + '/ord/order/getOrders', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.orderDetail = data.body.orders[0];
                }
            })
        }
    },
    created() {
        this.searchForm.orderCode = this.$route.params.id;
        this.getOrderDetail();
    }
}
</script>




