<template>
    <div class="wrap-login">  
        <div class="login-box">
            <h3>丰云综合平台</h3>
            <el-form :model="loginForm" :rules="rules" ref="loginForm" class="demo-ruleForm">
                <el-form-item prop="userAccount">
                    <i class="iconfont icon-user"></i>
                    <el-input type="text" v-model="loginForm.userAccount" auto-complete="off" placeholder="用户名/手机/邮箱" :class="loginForm.userAccount?'login-input active':'login-input'">
                    </el-input>
                </el-form-item>
    
                <el-form-item prop="userPassword">
                    <i class="iconfont icon-mima11"></i>
                    <el-input v-model="loginForm.userPassword" placeholder="密码" type="password" @keyup.enter.native="submitForm('loginForm')" :class="loginForm.userPassword?'login-input active':'login-input'">
                    </el-input>
                </el-form-item>
                <el-form-item prop="verifyCode" class="verifyCode" v-if="verifyShow">
                    <i class="iconfont icon-verify-code"></i>
                    <el-input v-model="loginForm.verifyCode" placeholder="验证码" type="text" @keyup.enter.native="submitForm('loginForm')" :class="loginForm.verifyCode?'login-input active':'login-input'">
                    </el-input>
                    <span class="verifycode-img" @click="imgSrc=imgSrc+'?tm='+Math.random()"><img :src="imgSrc" /></span>
                </el-form-item>
                <el-form-item class="login-btn">
                    <el-button type="primary" @click="submitForm('loginForm')" size="large" class="btn-login" :loading="loading" :disabled="loading">登入</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="copyright">
            <span>丰云综合平台所有版权</span>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      imgSrc: "/proxy/auth/api/verifyCode",
      verifyShow: false,
      loginForm: {
        userAccount: "",
        userPassword: "",
        verifyCode: ""
      },
      rules: {
        userAccount: [{ required: true, message: "请输入用户名" }],
        userPassword: [{ required: true, message: "请输入密码" }]
      },
      loading: false
    };
  },
  methods: {
    focusPwd() {},
    submitForm(formName) {
      let that = this;
      //表单验证

      this.$refs[formName].validate(valid => {
        if (valid) {
          //登入post
          this.loading = true;
          this.axios.post(this.myConfig.code.auth + "/api/login", {body: that.loginForm}).then(response => {
              this.loading = false;
              let data = response.data;
              if (data.head.code == "10000") {
                //登入成功
                window.localStorage.removeItem('login');
                window.localStorage.removeItem('act');
                window.localStorage.removeItem('setAuthSelect');
                window.localStorage.removeItem('userInfo');
                window.localStorage.removeItem('headMenu');
                window.localStorage.removeItem('hrf');
                that.$message({
                  type: "success",
                  message: "登入成功！",
                  duration: 1000,
                  onClose: function() {
                    //cookie 存储
                    // that.$cookie.set('token', data.body.token, { path: "/", domain: that.myConfig.domain });//开发
                    that.$cookie.set("token", data.body.token, { path: "/" }); //生产
                    //存储userName tonken
                    localStorage.setItem("login",JSON.stringify({username: data.body.userName,token: data.body.token}));

                    //获取权限后跳转
                    that.axios.post(that.myConfig.code.auth + "/api/authority", {}).then(response => {
                        let data = response.data;
                        that.axios.post("proxy/auth/api/auth/loginUserInfo", {body: { token: that.$cookie.get("token") }}).then(res => {
                            let _res = res.data;
                            if (data.head.code == "10000") {
                              localStorage.setItem("userInfo",JSON.stringify(_res.body));
                              if (data.head.code == "10000") {
                                //权限获取成功
                                if (data.body.menus.length == 0) {
                                  that.$message({
                                    type: "error",
                                    message: "该账号没有权限",
                                    duration: 1000
                                  });
                                  return;
                                }
                                localStorage.setItem(
                                  "headMenu",
                                  data.body.menus[0].menuId
                                );
                                localStorage.setItem(
                                  "setAuthSelect",
                                  JSON.stringify(data.body.menus[0])
                                );
                                that.$router.push("/");
                              }
                            }
                          });
                      });
                    //调试
                  }
                });
              } else {
                if (data.body.sub_code == "FY.AUTH.20005") {
                  that.verifyShow = true;
                }
                that.$message({
                  type: "error",
                  message: data.body.sub_msg
                });
              }
            });
        } else {
          that.$message({
            type: "error",
            message: "网络错误"
          });
          return false;
        }
      });
    }
  },
  created() {}
};
</script>


<style lang="less" scoped>
.wrap-login {
  overflow: hidden;
  height: inherit;
  background: url("../assets/common/images/login_bg.jpg") no-repeat 0 0;
  background-size: 100% 100%;
  position: relative;
  .copyright {
    position: absolute;
    bottom: 50px;
    left: 0;
    text-align: center;
    width: 100%;
  }
    .el-form-item__error {
    padding-left: 130px;
  }
}

.login-box {
  width: 500px;
  margin: 200px auto;
  text-align: center;
  padding: 20px 60px;
  h3 {
    color: #232323;
    font-size: 24px;
    margin-top: 10px;
  }
  .btn-login {
    display: block;
    width: 280px;
    margin-left: 125px;
    background: #ca1a1a;
    color: #fff;
    border-color: #ca1a1a;
    &:hover {
      background: #ea3131;
    }
    &:focus {
      background: #b30b0b;
    }
  }
  .iconfont {
    font-size: 24px;
    color: #9d9d9d;
    vertical-align: middle;
    margin-right: 5px;
  }
  .el-input {
    width: 280px;
  }
  .verifyCode {
    position: relative;
    cursor: pointer;
    .verifycode-img {
      position: absolute;
      top: 0px;
      left: 425px;
      display: inline-block;
      img {
        height: 38px;
      }
    }
  }
}
</style>
<style lang="less">
.login-input {
  &.active {
    .el-input__inner {
      border-color: #808080;
      background: #fff;
      color: #333;
    }
  }
  .el-input__inner {
    background: #d9d9d9;
    color: #a4a3a3;
    border-color: #bfbdbd;
    &:hover {
      border-color: #808080;
    }
    &:focus {
      border-color: #808080;
      background: #fff;
      color: #333;
    }
    &:-moz-placeholder {
      /* Mozilla Firefox 4 to 18 */
      color: #fefefe;
      opacity: 1;
    }

    &::-moz-placeholder {
      /* Mozilla Firefox 19+ */
      color: #fefefe;
      opacity: 1;
    }

    &:-ms-input-placeholder {
      color: #fefefe;
      opacity: 1;
    }

    &::-webkit-input-placeholder {
      color: #fefefe;
      opacity: 1;
    }
    &:focus {
    }
  }
}


</style>





