<template>
    <div class="wrap-inst">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>翻译管理</el-breadcrumb-item>
                <el-breadcrumb-item>翻译机构</el-breadcrumb-item>
            </el-breadcrumb>
            <!--新增机构-->
            <div class="add-inst fl-r">
                <el-button type="warning" class="add-inst-btn" @click="addForm_btn('')" size="mini">
                    <i class="iconfont icon-add"></i>新增</el-button>
            </div>
        </div>
        <!--新增翻译机构表单-->
        <el-dialog :title="addForm.instId==''?'新增翻译机构':'修改翻译机构'" :visible.sync="dialogAddForm" class="add-inst-form" size="tiny" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="100px" ref="addForm" :rules="addFormRules">
                <el-form-item label="机构名称" prop="instName">
                    <el-input v-model="addForm.instName" placeholder="机构名称"></el-input>
                </el-form-item>
                <el-form-item label="联系人" prop="contact">
                    <el-input v-model="addForm.contact" placeholder="联系人"></el-input>
                </el-form-item>
    
                <el-form-item label="电话" prop="phone">
                    <el-input v-model="addForm.phone" placeholder="联系电话"></el-input>
                </el-form-item>
                <el-form-item label="email" prop="email">
                    <el-input v-model="addForm.email" placeholder="联系人email"></el-input>
                </el-form-item>
                <el-form-item label="状态" prop="status">
                    <el-select v-model="addForm.status" placeholder="状态">
                        <el-option label="有效" value="1"></el-option>
                        <el-option label="无效" value="0"></el-option>
                    </el-select>
                </el-form-item>
    
                <el-form-item label="账号" prop="userAccount" v-if="addForm.instId==''">
                    <el-input v-model="addForm.userAccount" placeholder="账号"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="userPassword" v-if="addForm.instId==''">
                    <el-input v-model="addForm.userPassword" placeholder="密码" type="password"> </el-input>
                </el-form-item>
                <el-form-item label="确认密码" v-if="addForm.instId==''" prop="userPasswordCheck">
                    <el-input v-model="addForm.userPasswordCheck" placeholder="确认密码" type="password"></el-input>
                </el-form-item>
    
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddForm = false">取 消</el-button>
                <el-button type="primary" @click="submit_add">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 搜索 -->
        <div class="search">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <el-form-item label="机构名称">
                    <el-input v-model="searchForm.instName" placeholder="机构名称" size="small"></el-input>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt" size="">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
    
        <!--列表-->
        <el-table :data="instList" stripe style="width: 100%" v-loading="loading" class="report" border>
            <el-table-column label="ID" prop="instId" width="100"></el-table-column>
            <el-table-column label="机构名称" prop="instName"  width="120" show-overflow-tooltip></el-table-column>
            <el-table-column label="联系人" prop="contact" width="150" show-overflow-tooltip> </el-table-column>
            <el-table-column label="电话" prop="phone" width="150" show-overflow-tooltip> </el-table-column>
            <el-table-column label="email" prop="email" min-width="120" show-overflow-tooltip> </el-table-column>
            <el-table-column label="状态" width="90">
                <template slot-scope="scope">{{scope.row.status==1?'有效':'无效'}}</template>
            </el-table-column>
            <el-table-column label="用户账号" prop="account" width="120" show-overflow-tooltip> </el-table-column>
            <el-table-column label="创建时间" width="165">
                <template slot-scope="scope">{{scope.row.createTime|dateFormat}}</template>
            </el-table-column>
            <el-table-column label="操作" width="250" fixed="right" align="center">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="doUpdate(scope.row.instId)">修改</el-button>
                    <el-button size="small" type="danger" @click="doDelete(scope.row.instId)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    
        <!--分页-->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<style lang="less" scoped>
.wrap-inst {
    padding-right: 40px;
}
</style>

<script>
import { mapGetters } from 'vuex'
import format from 'date-format';
export default {
    data() {
        let checkPwd = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('请再次输入密码'));
            }
            setTimeout(() => {
                if (value !== this.addForm.userPassword) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            }, 500);
        };
        return {
            //加载
            loading: true,
            //查询
            searchForm: {
                instName: '',
                page: 1,
                pageSize: this.myConfig.pageRows
            },
            total: 0,
            //列表
            instList: [],
            //新增
            addForm: {
                instId: "",
                instName: "",
                contact: "",
                email: "",
                phone: "",
                status: '1',
                userAccount: "",
                userPassword: "",
                userPasswordCheck: ""
            },
            dialogAddForm: false,
            //表单验证
            addFormRules: {
                instName: [{ required: true, message: '请输入机构名称' }],
                contact: [{ required: true, message: '请输入联系人' }],
                email: [{ required: true, message: '请输入email' }],
                phone: [{ required: true, message: '请输入联系人电话' }],
                userAccount: [{ required: true, message: '请输入用户账号' }],
                userPassword: [{ required: true, message: '请输入用户密码' }],
                userPasswordCheck: [{ validator: checkPwd }]
            },
        }
    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps'
        })
    },
    filters: {
        dateFormat(date) {
            return format('yyyy-MM-dd hh:mm', new Date(date));
        }
    },
    methods: {
        //机构查询
        search_smt() {
            this.searchForm.page = 1;
            //去掉前后空格
            for (let i in this.searchForm) {
                if (typeof this.searchForm[i] == 'string') {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
            this.getInstList();
        },

        //机构列表获取
        getInstList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/translate/api/inst/query', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.instList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },

        //获取机构详情
        getInstDetail(id) {
            this.axios.post(this.myConfig.code.proxy + '/translate/api/inst/query', { body: { instId: id } }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.addForm = data.body.rows[0];
                    this.addForm.status = this.addForm.status.toString();
                    this.dialogAddForm = true;
                }
            })

        },

        //新增机构 
        addForm_btn() {
            this.dialogAddForm = true;
            this.addForm = {
                instId: "",
                instName: "",
                contact: "",
                email: "",
                phone: "",
                status: '1',
                userAccount: "",
                userPassword: "",
                userPasswordCheck: ""
            };
        },
        // 提交
        submit_add() {
            this.$refs.addForm.validate((validate) => {
                this.$refs.addForm.validate((validate) => {
                    this.axios.post(this.myConfig.code.proxy + '/translate/api/inst/save', { body: this.addForm }).then(response => {
                        let data = response.data;
                        if (data.head.code === '10000') {//新增成功
                            this.getInstList();
                            this.dialogAddForm = false;
                        }
                    })
                })
            })

        },
        //修改翻译机构
        doUpdate(id) {
            this.addForm.instId = id
            this.getInstDetail(id);
        },
        //删除翻译机构
        doDelete(id) {
            this.$confirm('您确定删除此机构?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.axios.post(this.myConfig.code.proxy + '/translate/api/inst/delete', { body: { instId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//删除成功
                        this.getInstList();
                        this.$message({
                            type: "success",
                            message: '删除成功',
                            duration: 1000
                        })
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除',
                    duration: 1000
                });
            });
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page = val;
            this.getInstList();
        }

    },
    mounted() {
        this.getInstList();
    }
}
</script>




