<template>
    <div class="wrap wrap-panel">
        <div class="dynamic panel-box" v-if="userType=='supplier'">
            <div class="panel-title">
                <i class="iconfont icon-dynamic"></i> <span class="txt">动态中心</span>
            </div>
            <div class="panel-content">
                <div class="dy-bar">
                    <div class="bar-title">
                        <i class="iconfont icon-note"></i> <span class="txt">订单模块</span>
                    </div>
                    <div class="bar-info">
                        <div class="item">
                            待发货订单
                            <router-link class="num" :to="'/main/jump?url='+encodeURIComponent('/fv/supplier/order.php?act=list&order_status=2')">{{panelInfo.order_waiting_for_shipping}}</router-link >                                       
                        </div>
                        <div class="item">
                            待确认发货包裹单
                            <router-link class="num" :to="'/main/jump?url='+encodeURIComponent('/fv/supplier/order.php?act=kuaidi_order_list&package_status=0')">{{panelInfo.package_waiting_for_confirm}}</router-link >
                        </div>                                   
                    </div>
                </div>
                <div class="dy-bar">
                    <div class="bar-title">
                        <i class="iconfont icon-note"></i> <span class="txt">商品模块</span>
                    </div>
                    <div class="bar-info">
                        <div class="item">
                            在售商品 <router-link to="/vendor/goods-list?search_audit=5&is_on_sale=1"  class="num">{{panelInfo.goods_on_sale}}</router-link>
                        </div>
                        <div class="item">
                            下架商品 <router-link to="/vendor/goods-list?is_on_sale=0"  class="num">{{panelInfo.goods_not_on_sale}}</router-link>
                        </div>
                        <div class="item">
                            未提交审核  <router-link to="/vendor/goods-list?search_audit=7 "  class="num">{{panelInfo.goods_not_approval}}</router-link>
                        </div>
                        <div class="item">
                            审核中商品 <router-link to="/vendor/goods-list?search_audit=1"  class="num">{{panelInfo.goods_approving}}</router-link>
                        </div>
                        <div class="item">
                            审核不通过   <router-link to="/vendor/goods-list?search_audit=3 "  class="num">{{panelInfo.goods_rejected}}</router-link>
                        </div>
                        <div class="item">
                            即将过期 <router-link to="/vendor/goods-list?sale_closing_date=2 "  class="num">{{panelInfo.goods_being_overdue}}</router-link>
                        </div>
                        <div class="item">
                            库存售空  <router-link to="/vendor/goods-list?is_qyt=0&is_on_sale=1 "  class="num">{{panelInfo.goods_out_of_stock}}</router-link>
                        </div>
                                     
                    </div>
                </div>
                <div class="dy-bar" style="border:none;">
                    <div class="bar-title">
                        <i class="iconfont icon-note"></i> <span class="txt">结算模块</span>
                    </div>
                    <div class="bar-info">
                        <div class="item">
                            待确认账单 <router-link :to="'/main/jump?url='+encodeURIComponent('/fv/supplier/settlementsheet.php?act=sheet_list&status=2')"  class="num">{{panelInfo.bill_waiting_for_confirm}}</router-link>
                        </div>                                                        
                    </div>
                </div>
            </div>
        </div>
        <div class="notice-box" v-if="userType=='supplier'">
            <div class="notice">
                <div class="title"><i class="iconfont icon-notice"></i>最新公告</div>
                <div class="list-box">
                    <div class="item" v-for="item in panelInfo.notices" :key="item.article_id">
                        <a :href="'/proxy/fv/supplier/notice.php?act=look&id='+item.article_id" target="_blank" class="txt"> {{item.title}}</a>
                        <div class="time">{{item.add_time}}</div>
                    </div>          
                </div>
                <div class="more">  <a href="/proxy/fv/supplier/notice.php?act=list" target="_blank">查看更多 ></a></div>
            </div>
            <div class="span"></div>
            <div class="help">
                <div class="title"><i class="iconfont icon-help-unfill"></i>帮助中心</div>
                <div class="list-box">
                    <div class="item"><a href="http://res.intmall.net/file/vendors/shipping_fee.pdf" class="txt" target="_blank"> 顺丰国际小包价格</a></div>
                    <div class="item"><a href="http://res.intmall.net/file/vendors/product_upload.pdf" class="txt"  target="_blank"> 顺丰经济小包价格</a></div>
                    <div class="item"><a href="http://res.intmall.net/file/vendors/order-guide.pdf" class="txt" target="_blank"> 顺丰国际特惠价格</a></div>
                    <div class="item"><a href="http://s1.pic.intmall.net/file/vendors/freight-guide.pdf" class="txt" target="_blank"> 顺丰国际标快价格</a></div>
                    <div class="item"><a href="http://dev-ibu-fyp.sit.sf-express.com:83/file/vendors/rumall_regulations.pdf" class="txt" target="_blank"> 丰卖网平台规则</a></div>
                </div>
                <!-- <div class="more"><a href="#">查看更多 ></a></div> -->
            </div>
        </div>
        <div class="bg" v-if="userType=='admin'">
            <img src="../../../assets/common/images/notice.jpg" alt="">
        </div>
        
    </div>
</template>

<script>
export default {
    data(){
        return {
            userType:'',
            panelInfo:{}
        }
    },
    methods:{
        getPanelInfo(){
            this.axios.post('/proxy/fv/supplier/goods.php?act=getReportData',{}).then(res=>{
                let data=res.data;
                if(data.head.code=='10000'){
                    this.panelInfo=data.body;
                }
            })
        }
    },
    created(){
        if (JSON.parse(window.localStorage.getItem("userInfo")).institutionTypeId === "SYSTEM_OPERATOR") {
             this.userType = "admin";
        }else{
            this.userType='supplier';
            this.getPanelInfo();
        }
        
    },
    beforeRouteLeave(to, from, next) {
        if(to.name=='vendor-goods-list'){
            window.localStorage.setItem('act',JSON.stringify({active:{key:["SJ_GOODS_M"],sub_key:"/vendor/goods-list"}}));
            
        }else if(to.name=='main-jump'){
            if(decodeURIComponent(to.fullPath).indexOf('/fv/supplier/order.php?act=list')>-1){
                window.localStorage.setItem('act',JSON.stringify({"active":{"key":["SJ_ORDER_M"],"sub_key":"/fv/supplier/order.php?act=list"}}));
            }else if(decodeURIComponent(to.fullPath).indexOf('/fv/supplier/settlementsheet.php?act=sheet_list')>-1){
                window.localStorage.setItem('act',JSON.stringify({"active":{"key":["SETTLEMENT_SHEET"],"sub_key":"/fv/supplier/settlementsheet.php?act=sheet_list"}}));
            }else if(decodeURIComponent(to.fullPath).indexOf('/fv/supplier/order.php?act=kuaidi_order_list')>-1){
                window.localStorage.setItem('act',JSON.stringify({"active":{"key":["SJ_ORDER_M"],"sub_key":"/fv/supplier/order.php?act=kuaidi_order_list"}}));
            }

        }
        next();
         
    }

}
</script>
<style lang="less" scoped>
.wrap-panel{
    padding: 20px;
    font-size: 14px;
    color: #222;
    .bg{
        text-align: center;
    }
    .panel-box{
        border:1px solid #ccc;
        margin-bottom: 15px;
        padding:0 20px;
        border-radius: 10px;
    }
    .panel-title{
        font-weight: bold;
        font-size: 14px;
        color:#333;
        padding: 10px 0;
        border-bottom: 1px solid #ccc;
        .iconfont{
            margin-right: 10px;
            font-size: 18px;
        }

    }
    .panel-content{
        .dy-bar{
            display: flex;
            padding-left: 15px;
            align-items: center;
            border-bottom: 1px solid #ccc;
            .bar-title{
                width: 200px;
                box-sizing: border-box;
                
                font-weight: bold;
                display: flex;
                align-items: center;
                .iconfont{
                    font-size: 22px;
                    font-weight: normal;
                    margin-right: 15px;
                    
                }         
            }
            .bar-info{
                flex:1;
                display: flex;
                flex-wrap: wrap;
               .item{
                    width: 170px;
                    min-height: 55px;
                    display: flex;
                    align-items: center;
                    .num{
                        color:#0000FF;
                        font-weight: bold;
                        margin-left: 10px;
                        display: flex;
                        align-items: center;
                        text-decoration: none;
                    }
               }
            }
        }
    }
    .notice-box{
        margin: 20px 0;
        display: flex;
        .notice,.help{
            flex:1;
            border:1px solid #ccc;
            padding:0 20px;
            border-radius: 10px;
            .title{
                font-weight: bold;
                font-size: 14px;
                color:#333;
                padding: 10px 0;
                border-bottom: 1px solid #ccc;
                .iconfont{
                    margin-right: 10px;
                    font-size: 18px;
                }
            }
        }
        .span{
            width: 20px;
        }
        .notice,.help{
           .list-box{
                padding: 10px 0;
                
           }
            .item{
                display: flex;
                height: 30px;
               
                .txt{
                    flex:1;
                    text-decoration: none;
                    color:#222;              
                    &::before{
                        display: inline-block;
                        content: ".";
                        vertical-align: bottom;
                        font-size: 20px;
                        margin-right: 10px;
                    }
                }
                .time{
                    width: 100px;
                }
            }
        }
        .more{
            text-align: right;
            padding-right: 15px;
            margin-bottom: 20px;
            a{
                color:#0000FF;
            }
        }
    }
}
</style>



