import Vue from 'vue'
import Router from 'vue-router'

//login compone
import Login from '@/components/Login.vue';

import NotFoundComponent from '@/components/NotFoundComponent.vue';

//router components
import RouterC from '@/components/Router.vue';

import router_goods from './router-goods.js';

import router_translate from './router-translate.js';
import router_auth from './router-auth.js';
import router_main from './router-main.js';
import router_vendor from './router-vendor.js';
import router_logistics from './router_logistics';
import router_ec from './router_ec';
import router_report from './router_report';
import router_order from './router-order.js';

import router_404 from './router-notFound.js';

let VueCookie = require('vue-cookie');

Vue.use(Router)
    //login router
let router_login = {
    path: '/login',
    name: 'login',
    component: Login
}

let _router = new Router({
    mode: 'history',
    routes: [
        router_login,
        router_goods,
        {
            path: '/',
            name: 'routerC-root',
            redirect: '/main',
            component: RouterC
        },
        {
            path: '/',
            name: 'routerC',
            component: RouterC,
            children: [
                router_auth,
                router_vendor,
                router_main,
                router_translate,
                router_logistics,
                router_ec,
                router_report,
                router_order,
                router_404
            ]
        },
        {
            path: '*',
            component: NotFoundComponent
        }

    ],
    // scrollBehavior(to, from, savedPosition) {
    //     console.log(to, from, savedPosition, 222222222);
    //     // return 期望滚动到哪个的位置
    // }
});

//登入路由控制
_router.beforeEach((to, from, next) => {
    setTimeout(() => {
        var _hmt = _hmt || [];
        (function() {
            var hm = document.createElement("script");
            hm.src = "https://hm.baidu.com/hm.js?2aaf9c2b13582c93b0bccb64c673f6d3";
            var s = document.getElementsByTagName("script")[0];
            s.parentNode.insertBefore(hm, s);
        })();
    }, 0)

    // console.log(VueCookie.get('token')) 登入验证
    if (to.path != '/login') {
        if (!VueCookie.get('token')) {
            next({ path: '/login' })
        } else {
            next();
        }
    } else {
        next();
    }

    if (from.name == 'vendor-freight-template') {

    }
})

export default _router;


