<template>
    <div class="wrap-router">
        <div class="header-c">
            <HeaderC></HeaderC>
        </div>
        <div class="main-c">
            <router-view></router-view>
        </div>
    
    </div>
</template>
<style lang="less" scoped>
.wrap-router {
    height: 100%;
    position: relative;
    color:#333;
    .header-c {
        position: absolute;
        top: 0;
        left: 0;
        height: 70px;
        width: 100%;
        background: #000;
    }
    .main-c {
        padding-top: 70px;
        overflow: hidden;
        height: inherit;
        box-sizing: border-box;
    }
}
</style>


<script>
import HeaderC from '@/components/Header.vue';


export default {
    components: {
        HeaderC
    },
    created() {
        // return  false;
        //获取权限数据
        this.axios.post(this.myConfig.code.auth + '/api/authority', {}).then(response => {
            let data = response.data;
            if (data.head.code == "10000") {//权限获取成功
                //存储权限列表
                this.$store.dispatch('setAuthority', data.body);
                this.$store.dispatch('setAuthSelect', data.body.menus[0]);  //一级菜单列表
                localStorage.setItem('headMenu', data.body.menus[0].menuId);
                localStorage.setItem('setAuthSelect', JSON.stringify(data.body.menus[0]));
                //存储操作列表
                let opsArr = [];
                let opsName = [];
                let getAllOps = function (arr) {
                    for (let i in arr) {
                        opsArr = opsArr.concat(arr[i].ops);
                        opsName.push(arr[i].menuUri)
                        if (arr[i].childMenu) {
                            getAllOps(arr[i].childMenu)
                        }
                    }
                }
                getAllOps(data.body.menus);
                for (let j in opsArr) {
                    opsName.push(opsArr[j].opUri)
                }
                opsName = Array.from(new Set(opsName))
                this.$store.dispatch('setAuthOps', opsName);

            }
        })
        // this.axios.post("http://localhost:5566/GoodsColor/getCurrency",{}).then(res=>{

        // })

    },
    watch:{
        '$route':function(){
          
        }
    }
    
}
</script>



