<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>翻译管理</el-breadcrumb-item>
                <el-breadcrumb-item>翻译审核统计</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <el-form-item label="审核人员">
                    <el-input v-model="searchForm.auditUser" placeholder="审核人员" size="small"></el-input>
                </el-form-item>
                <el-form-item label="审核时间始">
                    <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.auditStartDate" :picker-options="pickerOptions"></el-date-picker>
                </el-form-item>
                <el-form-item label="审核时间止">
                    <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.auditEndDate" :picker-options="pickerOptions"></el-date-picker>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--翻译列表-->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" border>
             <el-table-column type="index" width="50" ></el-table-column>
            <el-table-column label="审核人员" prop="auditUser" align="center"></el-table-column>
            <el-table-column label="机翻审核通过数" prop="autoCount" align="center"> </el-table-column>
            <el-table-column label="商家翻译审核通过数" prop="supplierCount" align="center"> </el-table-column>
            <el-table-column label="人翻审核通过数" prop="otherCount" align="center"> </el-table-column>
            <!-- <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="doAction(scope.row.auditUser)">详情</el-button>
                </template>
            </el-table-column> -->
        </el-table>
    
        <!-- 报表详情 -->
        <!-- <el-dialog :title="detailSearch.auditUser+'的报表详情'" :visible.sync="dialogDetailVisible" :close-on-click-modal="false" size="large">
            <el-table :data="detailData" stripe style="width: 100%" v-loading="loading2" height="400">
                <el-table-column property="goodsSn" label="商品编码" width="180"></el-table-column>
                <el-table-column property="goodsTitle" label="商品标题"></el-table-column>
                <el-table-column property="totalChar" label="译文字符总数" width="140"></el-table-column>
                <el-table-column property="acceptTime" label="接收任务时间" width="180"></el-table-column>
                <el-table-column property="auditTime" label="审核通过时间" width="180"></el-table-column>
            </el-table> -->
            <!--分页-->
            <!-- <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="detailSearch.page" :page-size="detailSearch.pageSize" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </el-dialog> -->
    
    </div>
</template>
<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            searchForm: {
                auditUser: '',
                auditStartDate: '',
                auditEndDate: ''
            },
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }

                }
            },
            //报表列表

            loading: false,
            total: 0,
            reportList: [],
            // 详情
            dialogDetailVisible: false,
            loading2: false,
            detailData: [],
            detailSearch: {
                auditUser: '',
                auditStartDate: '',
                auditEndDate: '',
                taskStatus: 4,
                page: 1,
                pageSize: this.myConfig.pageRows
            }
        }
    },
    methods: {
        search_smt() {
            //去掉前后空格
            for (let i in this.searchForm) {
                if (typeof this.searchForm[i] == 'string') {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
            this.getReportList();
        },
        //报表列表获取
        getReportList() {
            this.loading = true;
            if (this.searchForm.auditStartDate != '') {
                this.searchForm.auditStartDate = format('yyyy-MM-dd', new Date(this.searchForm.auditStartDate));
            }
            if (this.searchForm.auditEndDate != '') {
                this.searchForm.auditEndDate = format('yyyy-MM-dd', new Date(this.searchForm.auditEndDate));
            }
            this.axios.post(this.myConfig.code.proxy + '/translate/api/report/auditCount', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body;
                }
            })
        },
        //获得报表详情
        getDetailList() {
            this.loading2 = true;
            if (this.searchForm.auditStartDate != '') {
                this.detailSearch.auditStartDate = format('yyyy-MM-dd', new Date(this.searchForm.auditStartDate));
            } else {
                this.detailSearch.auditStartDate = '';
            }
            if (this.searchForm.auditEndDate != '') {
                this.detailSearch.auditEndDate = format('yyyy-MM-dd', new Date(this.searchForm.auditEndDate));
            } else {
                this.detailSearch.auditEndDate = '';
            }
            this.axios.post(this.myConfig.code.proxy + '/translate/api/task/admin/query', { body: this.detailSearch }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.total = data.body.total;
                    this.loading2 = false;
                    this.detailData = data.body.rows;

                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.detailSearch.page = val;
            this.getDetailList();
        },
        doAction(data) {
            this.detailSearch.auditUser = data;
            this.detailSearch.page = 1;
            this.dialogDetailVisible = true;
            this.getDetailList();
        }

    },
    created() {
        this.getReportList();
    }
}
</script>




