export default {
    //设置权限列表
    setAuthority({ commit }, authority) {
        commit('setAuthority', authority);
    },
    setAuthSelect({ commit }, auth) {
        commit('setAuthSelect', JSON.parse(localStorage.getItem('setAuthSelect')));
    },
    setAuthOps({ commit }, ops) {
        commit('setAuthOps', ops)
    },
    setCollapse({ commit }) {
        commit('setCollapse');
    },
    setScrollTop({ commit }, h) {
        commit('setScrollTop', h)
    },
    //设置翻译状态与描述
    setTranslateManage({ commit }, obj) {
        commit('setTranslateManage', obj)
    },
    setTranslateMyTask({ commit }, obj) {
        commit('setTranslateMyTask', obj)
    },
    setTranslateSearch({ commit }, data) {
        commit('setTranslateSearch', data)
    },
    // 公告处理
    // 商品有效期公告
    setNoticeTerm({ commit }, data) {
        commit('setNoticeTerm', data)
    },
    // 商品有效期公告
    setGoodsBatch({ commit }, data) {
        commit('setGoodsBatch', data)
    },
}


