//商家 iframe  模块 goods 模块

const SysMain = r => require.ensure([], () => r(require('@/components/sys/main.vue')), 'sys-main');
const Main = r => require.ensure([], () => r(require('@/components/sys/goods/main.vue')), 'main');



const Vshortcut = r => require.ensure([], () => r(require('@/components/sys/goods/shortcut.vue')), 'goods-shorcut');
const GoodsList = r => require.ensure([], () => r(require('@/components/sys/goods/goods-list.vue')), 'goods-list');
const GoodsRecycle = r => require.ensure([], () => r(require('@/components/sys/goods/recycle-list.vue')), 'goods-recycle');
const GoodsBatchEdit = r => require.ensure([], () => r(require('@/components/sys/goods/goods-batchEdit.vue')), 'goods-batchedit');



const UploadHelp = r => require.ensure([], () => r(require('@/components/sys/goods/upload-help.vue')), 'goods-shortcut');
//首页


//商品快捷设置
let router_shortcut = {
    path: 'shortcut/:type/:id',
    name: 'goods-shortcut',
    component: Vshortcut
}

//商品列表页面
let router_goods_list = {
    path: 'list',
    name: 'goods-list',
    component: GoodsList
}

//商品批量修改
let router_goods_batchEdit = {
    path: 'batch-edit',
    name: 'goods-batch-edit',
    component: GoodsBatchEdit
}

//商品审核列表
let router_goods_list_audit = {
        path: 'list-audit',
        name: 'goods-list-audit',
        component: GoodsList
    }
    //商品回收站

let router_goods_recycle = {
    path: 'recycle',
    name: 'goods-recycle',
    component: GoodsRecycle
}


//商品颜色
let router_goods_upload_help = {
    path: 'upload-help',
    name: 'goods-upload-help',
    component: UploadHelp
}


let router_goods = {
    path: '/goods',
    name: 'goods',
    redirect: '/goods/index',
    component: Main,
    children: [router_shortcut, router_goods_list, router_goods_list_audit, router_goods_recycle, router_goods_batchEdit,router_goods_upload_help]
}


export default router_goods


