<template>
  <div class="wrap">
    <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>安全管理</el-breadcrumb-item>
        <el-breadcrumb-item>角色管理</el-breadcrumb-item>
      </el-breadcrumb>
      <!--新增角色-->
      <div class="add-menu fl-r" v-if="authOps.includes('/auth/api/role/save')">
        <el-button type="warning" class="add-menu-btn" @click="addForm_btn('')" size="mini">
          <i class="iconfont icon-add"></i>新增角色</el-button>
      </div>
    </div>
  
    <div class="search">
      <el-form :inline="true" :model="searchForm" class="demo-form-inline">
        <i class="el-icon-search"></i>
        <el-form-item label="角色名">
          <el-input v-model="searchForm.roleName" placeholder="角色名称" size="small"></el-input>
        </el-form-item>
        <el-form-item class="search-btn">
          <el-button type="primary" @click="search_smt" >查询</el-button>
        </el-form-item>
      </el-form>
    </div>
  
    <div class="menu-list">
  
      <!--新增角色表单-->
      <el-dialog :title="roleId==''?'新增角色':'修改角色'" :visible.sync="dialogAddForm" size="tiny" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="100px" ref="addForm">
          <el-form-item label="角色名称" prop="roleName" :rules="{required:true,message:'请输入角色名称'}">
            <el-input v-model="addForm.roleName" placeholder="角色名称"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDec" :rules="{required:true,message:'请输入角色描述'}">
            <el-input v-model="addForm.roleDec" placeholder="角色描述"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogAddForm = false">取 消</el-button>
          <el-button type="primary" @click="submit_addRole">确 定</el-button>
        </div>
      </el-dialog>
  
      <!--角色列表-->
      <el-table :data="roleList" stripe style="width: 100%" v-loading="loading" border >
        <el-table-column label="ID" prop="roleId"></el-table-column>
        <el-table-column label="角色名" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDec"> </el-table-column>
        <el-table-column label="操作" width="250">
          <template slot-scope="scope">
            <i title="分配权限" class="primary iconfont icon-subcontractor" @click="doAction(scope.row.roleId)" v-if="authOps.includes('/auth/api/role/authority/save')"></i>
            <i title="修改" class="primary iconfont icon-update" @click="doUpdate(scope.row.roleId)" v-if="authOps.includes('/auth/api/role/update')"></i>
            <i title="删除" class="iconfont icon-delete danger" @click="doDelete(scope.row.roleId)" v-if="authOps.includes('/auth/api/role/delete')"></i>
          </template>
        </el-table-column>
      </el-table>
  
      <!--分页-->
      <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
        <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.rows" layout="total, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
  
      <!--角色分配-->
      <el-dialog title="角色分配" :visible.sync="dialogRoleDistribution" size="small" class="dialogRoleDistribution" :close-on-click-modal="false">
        <div class="tree">
          <el-tree :data="authorityList" show-checkbox node-key="id" :default-checked-keys="authorityListed" :props="defaultProps" ref="roleTree" :render-content="renderContent">
          </el-tree>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogRoleDistribution = false">取 消</el-button>
          <el-button type="primary" @click="roleDistribution">确 定</el-button>
        </span>
      </el-dialog>
  
    </div>
  
  </div>
</template>

<style lang="less" scoped>
.wrap {
 padding-right: 40px;
}
</style>
<style lang="less">
.dialogRoleDistribution {
  .el-tree {
    height: 400px;
    overflow-x: auto; 
  }
}
</style>


<script>
import { mapGetters } from 'vuex'
export default {
  data() {
    return {
      //加载
      loading: true,
      //角色查询
      searchForm: {
        roleName: '',
        page: 1,
        rows: this.myConfig.pageRows
      },
      total: 0,

      //角色列表
      roleList: [],

      //角色id
      roleId: '',
      //新增角色
      addForm: {
        roleId: "",
        roleName: "",
        roleDec: ""
      },
      dialogAddForm: false,
      dialogRoleDistribution: false,
      roleId: "",
      authorityList: [],
      authorityListed: [],
      defaultProps: {
        children: 'child',
        label: 'label',
      },
      checkedOps: [],
      checkedMenus: []

    }
  },
  computed: {
    ...mapGetters({
      authSelect: 'getAuthSelect',
      authOps: 'getAuthOps'
    })
  },
  methods: {

    //角色查询
    search_smt() {
      this.searchForm.page=1;
      this.getRoleList();
    },

    //角色列表获取
    getRoleList() {
      this.loading = true;
      this.axios.post(this.myConfig.code.auth + '/api/role/list', { body: this.searchForm }).then(response => {
        let data = response.data;
        if (data.head.code == '10000') {
          this.loading = false;
          this.roleList = data.body.rows;
          this.total = data.body.total;
        }
      })
    },

    //获取角色详情
    getRoleDetail(id) {
      this.axios.post(this.myConfig.code.auth + '/api/role/detail', { body: { roleId: id } }).then(response => {
        let data = response.data;
        if (data.head.code == '10000') {
          this.addForm = data.body;
          this.roleId = this.addForm.roleId;
          this.dialogAddForm = true;
        }
      })

    },

    //新增角色
    addForm_btn() {
      this.dialogAddForm = true;
      this.roleId = '';
      this.addForm = {
        roleId: "",
        roleName: "",
        roleDec: ""
      };
    },
    //新增角色表单提交
    submit_addRole() {
      this.$refs.addForm.validate((validate) => {
        if (this.roleId == '') {//新增角色
          this.axios.post(this.myConfig.code.auth + '/api/role/save', { body: this.addForm }).then(response => {
            let data = response.data;
            if (data.head.code === '10000') {//新增成功
              this.getRoleList();
              this.dialogAddForm = false;
            } else {//新增失败
              this.$message({
                type: "error",
                message: data.body.sub_msg,
                duration: 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {//修改角色
          this.axios.post(this.myConfig.code.auth + '/api/role/update', { body: this.addForm }).then(response => {
            let data = response.data;
            if (data.head.code === '10000') {//新增成功
              this.getRoleList();
              this.dialogAddForm = false;
            } else {//新增失败
              this.$message({
                type: "error",
                message: data.body.sub_msg,
                duration: 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },

    //修改角色
    doUpdate(id) {
      this.roleId = id;
      this.getRoleDetail(id);
    },
    //删除角色
    doDelete(id) {
      this.$confirm('您确定删除此角色?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.axios.post(this.myConfig.code.auth + '/api/role/delete', { body: { roleId: id } }).then(response => {
          let data = response.data;
          if (data.head.code === '10000') {//删除成功
            this.getRoleList();
            this.$message({
              type: "success",
              message: '删除成功',
              duration: 1000
            })
          } else {//新增失败
            this.$message({
              type: "error",
              message: data.body.sub_msg,
              duration: 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          duration: 1000
        });
      });
    },

    //角色权限分配
    doAction(id) {
      this.dialogRoleDistribution = true;
      this.roleId = id;
      //获取角色权限
      this.axios.post(this.myConfig.code.auth + '/api/role/authority/list', { body: { roleId: id } }).then(response => {
        let data = response.data.body.menus;

        //获取节点数据
        let getTree = function (arr) {
          let myarr = [];
          for (let a in arr) {
            var tmp = {};
            var chilArray = [];
            tmp.label = arr[a].menuName;
            tmp.id = arr[a].menuId;
            tmp.type = "menu";
            tmp.parent = arr[a].parentId;
            if (arr[a].childMenu.length > 0) {
              chilArray = getTree(arr[a].childMenu);
            }
            if (arr[a].ops.length > 0) {
              for (let b in arr[a].ops) {
                var tmpOp = {};
                tmpOp.id = arr[a].ops[b].opId;
                tmpOp.label = arr[a].ops[b].opName;
                tmpOp.type = 'ops';
                tmpOp.parent = arr[a].menuId;
                chilArray.push(tmpOp);
              }
            }
            tmp.child = chilArray
            myarr.push(tmp)
          }
          return myarr;
        }
        let authorityList = getTree(data);
        console.log(getTree(data),9999)

        //获取选择节点数据，末端节点数据
        let menuIds = response.data.body.roleMenuIds;
        let menuIded = [];
        let getAuthorityListed = function (arr, id) {
          if (arr.length > 0) {
            for (let i in arr) {
              if (arr[i].id == id) {
                if (arr[i].child) {
                  if (arr[i].child.length == 0) {
                    menuIded.push(arr[i].id)
                  }
                } else {
                  aa.push(arr[i].id)
                }

              }
              if (arr[i].child) {
                getAuthorityListed(arr[i].child, id)
              }
            }
          }
        }
        for (let j in menuIds) {
          getAuthorityListed(authorityList, menuIds[j])
        }

        this.authorityListed = menuIded.concat(response.data.body.roleOpIds);

        //所有节点
        this.authorityList = authorityList;



      }).catch(error => {
        console.log(error)
      })
    },


    //render 图标
    renderContent(h, { node, data, store }) {
      return h('span', [
        h('span', {}, data.label),
        h('i', {
          style: { 'marginLeft': '5px', "fontSize": '14px' },
          class: data.type == 'menu' ? 'iconfont icon-caidan-copy' : 'iconfont icon-setting'
        })
      ]);
    },
    //修改权限分配
    roleDistribution() {
      let nodeList = this.authorityList.concat([]);

      let checkedNodes = this.$refs.roleTree.getCheckedNodes();
      //拆分 menu ops
      let menus = [];//菜单
      let ops = [];//操作
      let parents = [];//所有父节点
      for (let i in checkedNodes) {
        if (checkedNodes[i].type == 'ops') {
          ops.push(checkedNodes[i].id)
        } else {
          menus.push(checkedNodes[i].id);
        }
        if (checkedNodes[i].parent) {
          parents.push(checkedNodes[i].parent);

        }
      }

      parents = Array.from(new Set(parents));
      
      //递归获取 其父节点id
      let parentId = "";
      let parentArr = [];
      let getCheckedNodes = function (arr) {
        if (arr.length > 0) {
          for (let k in arr) {
            if (arr[k].id == parentId) {
              if (arr[k].parent) {
                parentId = arr[k].parent;
                parentArr.push(parentId)
              } else {
                parentId = arr[k].parent;
              }
            } else {
              if (arr[k].child && arr[k].child.length > 0) {
                getCheckedNodes(arr[k].child)
              }
            }

          }
        }
      }

      //循环获取其所有父节点id
      let getAll = function () {
        if (parentId) {
          getCheckedNodes(nodeList);
          getAll()
        }
      }

      //获取所有 menu 的父节点id
      for (let h in parents) {
        parentId = parents[h];
        getAll();
      }
      this.checkedMenus = Array.from(new Set(parentArr.concat(parents).concat(menus)));
      this.checkedOps = ops;
      this.axios.post(this.myConfig.code.auth + '/api/role/authority/save', {
        body: {
          roleId: this.roleId,
          roleOpIds: this.checkedOps,
          roleMenuIds: this.checkedMenus
        }
      }).then(response => {
        if (response.data.head.code == '10000') {
          this.dialogRoleDistribution = false;
          this.$router.go(0);//重新加载权限数据
        }
      })


    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page = val;
      this.getRoleList();
    }

  },
  created() {
    this.getRoleList();


  }
}
</script>




