<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>物流管理</el-breadcrumb-item>
                <el-breadcrumb-item :to="{path:'/logistics/package-list'}">包裹单列表</el-breadcrumb-item>
                <el-breadcrumb-item>包裹单详情</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--包裹详情-->
        <div class="package-info">
            <div class="item">
                <span class="label">包裹单号:</span><span class="txt">{{packageDetail.packageCode}}</span>
            </div>
            <div class="item">
                <span class="label">订单号:</span><span class="txt">{{packageDetail.orderCode}}</span>
            </div>
            <div class="item">
                <span class="label">运单号:</span><span class="txt">{{packageDetail.waybillNo}}</span>
            </div>
            <div class="item">
                <span class="label">代理运单号:</span><span class="txt">{{packageDetail.agentWaybillNo}}</span>
            </div>
            <div class="item">
                <span class="label">是否签收:</span><span class="txt">{{packageDetail.isSigned==0?'否':'是'}}</span>
            </div>
            <div class="item">
                <span class="label">创建人:</span><span class="txt">{{packageDetail.createUserName}}</span>
            </div>
            <div class="item">
                <span class="label">创建时间:</span><span class="txt">{{packageDetail.createTime|dateFormat}}</span>
            </div>
            <div class="item">
                <span class="label">物流类型:</span><span class="txt">{{packageDetail.logisticsName}}</span>
            </div>
            <div class="item">
                <span class="label">发货仓库:</span><span class="txt">{{packageDetail.warehouseName}}</span>
            </div>
        </div>
        <el-table :data="packageDetail.items" stripe style="width: 100%" >
            <el-table-column label="sku编码" prop="skuCode" align="center" width="180"></el-table-column>
            <el-table-column label="sku名称" prop="skuName" align="center" width="180"> </el-table-column>
            <el-table-column label="sku数量" prop="skuName" align="center" width="150"> </el-table-column>
            <el-table-column label="sku重量(g)" prop="weight" align="center"> </el-table-column>
        </el-table>
    </div>
</template>

<style lang="less" scoped>
.warp {
    padding-right: 40px;
}
.package-info{
    padding-bottom: 20px;
    font-size: 14px;
    width: 60%;
    .item{
        padding: 10px;
        .label{
            display: inline-block;
            width: 100px;
            padding-right: 30px;
            color: #1f2d3d; 
            font-weight: bold;
            text-align: right;
        }
    }
}
</style>


<script>
import format from 'date-format';
export default {
    data() {
        return {
            searchForm: {
                packageCode: '',
                page: 1,
                pageSize: this.myConfig.pageRows
            },
            packageDetail: {}
        }
    },
    filters: {
        dateFormat(date) {
            return format('yyyy-MM-dd hh:mm', new Date(date));
        }
    },
    methods: {
        getPackageDetail() {
            this.axios.post(this.myConfig.code.proxy + '/logistics/package/query', { body: { queryReq: this.searchForm } }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.packageDetail = data.body.packages[0];         
                }
            })
        }
    },
    created() {
        this.searchForm.packageCode = this.$route.params.id;
        this.getPackageDetail();
    }
}
</script>




