<template>
    <div class="wrap-sys-main">
        <div class="menu-main-box">
            <el-menu  class="el-menu-vertical-demo menu-box"  @select="menuSelect" :unique-opened="true" :collapse="isCollapse" :default-active="active.sub_key" :default-openeds="active.key" @open="menuOpen" background-color="#373737" text-color="#fff"  active-text-color="#FFD04B">
                <el-menu-item index="/main/panel">
                    <i class="el-icon-menu"></i>
                    <span slot="title">Dashboard</span>
                </el-menu-item>     
                    <el-menu-item v-for="(val,key) in authSelect.childMenu" :key="key" :index="val.menuUri" v-if="val.childMenu.length==0" >
                        <i :class="'el-icon-menu'" :title="val.menuName"></i><span slot="title">{{val.menuName}}</span>
                    </el-menu-item>
                    <el-submenu :index="val.menuId" v-else-if="val.childMenu.length>0">
                        <template slot="title">
                            <i :class="'el-icon-menu'" :title="val.menuName"></i>
                            <span slot="title">{{val.menuName}}</span>
                        </template>
                        <el-menu-item :index="val2.menuUri" v-for="(val2,key2) in val.childMenu" :key="key2">{{val2.menuName}}</el-menu-item>                    
                    </el-submenu>           
            </el-menu>           
            <div class="main-box">
                <div class="collapse-box">
                    <i :class="!isCollapse?'iconfont icon-shousuo':'iconfont icon-zhankai'" @click="tabCollapse"></i>
                </div>
                <div class="route-main-box">
                    <router-view></router-view>
                </div>           
            </div>
        </div>
    </div>
</template>
<script>

import { mapGetters } from 'vuex'


export default {
    data() {
        return {
            //激活菜单
            isCollapse:false,
            active:{
                key:[],
                sub_key:''
            },
            test:[1,2,3]
        }
    },
    methods: {
        menuSelect(key) {
            this.active=localStorage.getItem('act')?JSON.parse(localStorage.getItem('act')).active:{key:[],sub_key:''};
            this.active.sub_key=key;
            localStorage.setItem('act', JSON.stringify({active:this.active}));
            let routerArr = key.split('/');
            //菜单跳转
            if (key.indexOf('http') != -1 || key.indexOf('https') != -1) {//外连接               
                localStorage.setItem('hrf', JSON.stringify({ uri: key, time: new Date().getTime() }))
                this.$router.push({ path: '/main/iframe', query: { tm: new Date().getTime() } });
            } else if (routerArr[1] == 'fv') {//fv模块
                localStorage.setItem('hrf', JSON.stringify({ uri: key, time: new Date().getTime() }))
                this.$router.push({ path: '/main/iframe', query: { tm: new Date().getTime() } })
            }else if (routerArr[1] == 'goods') {//fv模块
                localStorage.setItem('hrf', JSON.stringify({ uri: key, time: new Date().getTime() }))
                this.$router.push({ path: '/main/iframe', query: { tm: new Date().getTime() } })
            } else {//自定义页面
                this.$router.push(key);
            }
        },
        tabCollapse() {
            // 
            this.isCollapse=!this.isCollapse;
            return false
            this.$store.dispatch('setCollapse')
        },
        menuOpen(key){
            this.active=localStorage.getItem('act')?JSON.parse(localStorage.getItem('act')).active : {key:[],sub_key:''};
            this.active.key=[key];
            localStorage.setItem('act', JSON.stringify({active:this.active}));
        }

    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps',
            collapse: 'getCollapse',
            
        })
    },
    
    created(){
        this.active=localStorage.getItem('act')?JSON.parse(localStorage.getItem('act')).active:{key:[],sub_key:''};
        // return false    
    },
    watch:{
        "$route":function(){
            this.active=localStorage.getItem('act')?JSON.parse(localStorage.getItem('act')).active:{key:[],sub_key:''};
       
        }
    }
    


}
</script>


<style lang="less" scoped>
.wrap-sys-main,
.el-row {
    height: 100%;
}

.wrap-sys-main {
     height: 100%;
    .menu-main-box{
        display: flex;
         height: 100%;
        .menu-box{    
            max-height: 100%;
            overflow-y: auto;            
        }
        .menu-box:not(.el-menu--collapse){
            width: 240px;         
        }
        .main-box{
            position: relative;
            flex:1;
            overflow: hidden;
            box-sizing: border-box;
            padding-left: 15px;
            height: 100%;
            .route-main-box{
                max-height: 100%;
                height: 100%; 
                overflow-y: auto;
                box-sizing: border-box;
                padding-top: 10px;  
                padding-bottom: 20px;
            }       
        }
        .collapse-box{
            position: absolute;
            left: 0;
            top: 0;
            background:#fff;
            color:#333;
            .iconfont{
                font-size: 22px;
            } 
        }
    }
    .el-menu--collapse>.el-menu-item span, .el-menu--collapse>.el-submenu>.el-submenu__title span {
    height: 0;
    width: 0;
    overflow: hidden;
    visibility: hidden;
    display: inline-block;
}
.el-menu--collapse>.el-menu-item .el-submenu__icon-arrow, .el-menu--collapse>.el-submenu>.el-submenu__title .el-submenu__icon-arrow {
    display: none;
}
}
</style>



<style lang="less">




//富文本
.vue-html5-editor {
    table {
        border-spacing: 0px;
        border-collapse: collapse;
        width: 100%;
        max-width: 100%;
        margin-bottom: 0px;
        border: 1px solid rgb(221, 221, 221);
        line-height: 20px;
        background-color: transparent;
    }
    td {
        padding: 8px;
        line-height: 1.42857;
        vertical-align: top;
        border: 1px solid;
        border-color: rgb(221, 221, 221);
    }
    .toolbar>ul>li .icon {
        width: 24px;
        height: 36px;
        line-height:normal;
    }
}
</style>




