import Vue from 'vue'

//引入vuex
import Vuex from 'vuex';
import store from './vuex/store';

//引入配置文件
import config from './config/baseConfig';
import editorOptions from './config/editor';

//时间格式化
import format from 'date-format';
Vue.prototype.format = format;

Vue.use(Vuex);

//vue cookie 插件
let VueCookie = require('vue-cookie');
Vue.use(VueCookie);

import VueHtml5Editor from 'vue-html5-editor'
Vue.use(VueHtml5Editor, editorOptions);

import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload)
import iView from 'iview';
Vue.use(iView);

// or with options
Vue.use(VueLazyload, {
    preLoad: 1.3,
    error: '@/assets/images/error.jpg',
    loading: '@/assets/images/loading.gif',
    attempt: 1
})

Vue.prototype.myConfig = config;

//引入elemntui
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
//导入css
import './assets/common/common.less'
import './assets/common/font/iconfont.css'

Vue.use(ElementUI)
import App from './App'

import router from './router'

//加载 decimal
import {Decimal} from 'decimal.js';

Vue.prototype.Decimal = Decimal;

//导入axios
import axios from 'axios';

Vue.prototype.axios = axios.create({
    baseURL: config.baseURL,
    data: {
        "head": {
            "version": "1.0.0", //版本控制
            "timeStamp": format.asString('yyyyMMddhhmmss', new Date())
        }
    },
    withCredentials: true,
    headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
    }
})

//请求拦截
Vue.prototype.axios.interceptors.response.use((response) => {
    let data = response.data;
    if (data.head && data.head.code != '10000' && _Vue.$route.name != 'login') {
        ElementUI.Message({
            type: "error",
            message: response.data.body.sub_msg,
            onClose() {
                if (response.data.head.code == '40006' || response.data.body.sub_code == "FY.AUTH.10007" || response.data.body.sub_code == "FY.AUTH.10006") {
                    _Vue.$cookie.delete('token', {path: "/"}); //开发
                    window.localStorage.removeItem('login');
                    _Vue.$router.push('/login')
                }
            }
        });

    }
    return response;
}, function (error) {
    ElementUI.Message.error('网络错误！');
    return Promise.reject(error);
});
Vue.config.productionTip = false
/* eslint-disable no-new */
let _Vue = new Vue({
    el: '#app',
    axios,
    router,
    template: '<App/>',
    components: {
        App
    },
    store
})


