<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>安全管理</el-breadcrumb-item>
                <el-breadcrumb-item>菜单管理</el-breadcrumb-item>
            </el-breadcrumb>
            <!--新增菜单-->
            <div class="add-menu fl-r" v-if="authOps.includes('/auth/api/menu/save')">
                <el-button type="warning" class="add-menu-btn" @click="addForm_btn('')" size="mini">新增菜单</el-button>
            </div>
        </div>
    
        <!--查询-->
        <div class="search">
            <el-form :inline="true" :model="menuSearch" class="demo-form-inline">
                
                <el-form-item label="菜单名称">
                    <el-input v-model="menuSearch.menuName" placeholder="菜单名称" size="small"></el-input>
                </el-form-item>
                <el-form-item label="父菜单">
                    <el-input v-model="menuSearch.parentMenuName" placeholder="父菜单" size="small"></el-input>
                </el-form-item>
                <el-form-item label="机构类型">
                    <el-select v-model="menuSearch.institutionTypeId" placeholder="机构类型" size="small">
                        <el-option label="所有" value=""></el-option>
                        <el-option :label="val.institutionTypeName" :value="val.institutionTypeId" v-for="(val,key) in instList" :key="key"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
    
        <!--菜单列表-->
        <div class="menu-list">
    
            <!--新增菜单表单-->
            <el-dialog :title="menuId==''?'新增菜单':'修改菜单'" :visible.sync="dialogAddForm" class="add-menu-form" :close-on-click-modal="false">
                <el-form :model="addForm" label-width="100px" ref="addForm" :rules="addFormRules">
                    <el-form-item label="菜单编码" prop="menuId">
                        <el-input v-model="addForm.menuId" placeholder="菜单编码" :disabled="menuId!=''"></el-input>
                    </el-form-item>
                    <el-form-item label="菜单名称" prop="menuName">
                        <el-input v-model="addForm.menuName" placeholder="菜单名称"></el-input>
                    </el-form-item>
                    <el-form-item label="菜单Uri" prop="menuUri">
                        <el-input v-model="addForm.menuUri" placeholder="菜单Uri"></el-input>
                    </el-form-item>
                    <el-form-item label="机构类型" prop="institutionTypeId">
                        <el-select v-model="addForm.institutionTypeId" placeholder="机构类型" size="small" @change="instChecked">
                            <el-option :label="val.institutionTypeName" :value="val.institutionTypeId" v-for="(val,key) in instList" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="父级菜单" prop="parentId">
                        <el-select v-model="addForm.parentId" placeholder="请选择父级菜单" size="small" filterable >
                            <el-option :label="val.menuName" :value="val.menuId" v-for="(val,key) in parentList" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
    
                    <el-form-item label="菜单图标" prop="menuIco">
                        <el-select v-model="addForm.menuIco" placeholder="菜单图标" size="small">
                            <el-option :value="item.value" :label="item.label" v-for="item in iconfontList" :key="item.value">
                                <i :class="'iconfont icon-'+item.value"></i>
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="菜单状态">
                        <el-radio class="radio" v-model="addForm.status" label="1">有效</el-radio>
                        <el-radio class="radio" v-model="addForm.status" label="0">无效</el-radio>
                    </el-form-item>
                    <el-form-item label="是否显示">
                        <el-radio class="radio" v-model="addForm.isOpen" label="1">是</el-radio>
                        <el-radio class="radio" v-model="addForm.isOpen" label="0">否</el-radio>
                    </el-form-item>
                    <el-form-item label="排列序号" prop="menuSeq">
                        <el-input v-model.number="addForm.menuSeq" placeholder="排列序号" type="text"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogAddForm = false">取 消</el-button>
                    <el-button type="primary" @click="submit_addMenu">确 定</el-button>
                </div>
            </el-dialog>
    
            <!--菜单列表-->
            <el-table :data="menuData" stripe style="width: 100%" v-loading="menu_list_loading">
                <el-table-column label="菜单代码" prop="menuId"></el-table-column>
                <el-table-column label="菜单名称" prop="menuName"></el-table-column>
                <el-table-column label="菜单图标" align="center" width="90">
                    <template slot-scope="scope">
                        <i :class="'iconfont icon-'+scope.row.menuIco"></i>
                    </template>
                </el-table-column>
                <el-table-column label="菜单Uri" prop="menuUri"></el-table-column>
                <el-table-column label="菜单等级" width="100" prop="menuLevel"> </el-table-column>
                <el-table-column label="机构类型" prop="institutionTypeName"></el-table-column>
                <el-table-column label="父菜单" prop="parentMenuName"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <i title="操作管理" class="primary iconfont icon-jiecunweihu1" @click="doAction(scope.row.menuId)" v-if="authOps.includes('/auth/api/menu/op/list')"></i>
                        <i title="修改" class="primary iconfont icon-update" @click="doUpdate(scope.row.menuId)" v-if="authOps.includes('/auth/api/menu/update')"></i>
                        <i title="删除" class="danger iconfont icon-delete" @click="doDelete(scope.row.menuId)" v-if="authOps.includes('/auth/api/menu/delete')"></i>
                    </template>
                </el-table-column>
            </el-table>
    
            <!--分页-->
            <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="menuSearch.page" :page-size="menuSearch.rows" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<style lang="less" scoped>
// table
.iconfont {
    font-size: 20px;
}

.wrap {
    padding-right: 40px;
}

.add-menu-form {
    .el-input {
        width: 80%;
        display: inline-block;
    }
}
</style>
<style lang="less">
.add-menu-form {
    .el-dialog--small {
        width: 40%;
    }

    .el-dialog {
        height: 550px;
    }
    .el-dialog__body {
        overflow-x: auto;
        height: 370px;
    }
    .dialog-footer {
        padding-right: 35px;
    }
}
</style>


<script>
import { mapGetters } from 'vuex';
import iconList from '../../../json/iconList.json';
export default {
    data() {
        let checkMenuSeq = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('排列序号不能为空'));
            }
            setTimeout(() => {
                if (!Number.isInteger(value)) {
                    callback(new Error('请输入数字值'));
                } else {
                    if (value < 1 || value > 125) {
                        callback(new Error('数值范围介于1-125'));
                    } else {
                        callback();
                    }
                }
            }, 300);
        };
        return {
            //iconfont,
            iconfontList: iconList.iconList,
            //加载loading
            "menu_list_loading": true,

            //列表查询
            menuSearch: {
                menuName: '',
                parentMenuName: '',
                institutionTypeId: '',
                //分页
                page: 1,
                rows: this.myConfig.pageRows
            },
            total: 0,
            //menuID
            menuId: '',

            //所有机构类型列表
            instList: [],
            instTypeId: '',

            //菜单列表
            menuData: [],

            //所有父级菜单
            parentList: [],

            //新增菜单
            dialogAddForm: false,
            addForm: {
                institutionTypeId: "",
                isOpen: 1,
                menuIco: "",
                menuId: "",
                menuName: "",
                menuSeq: '',
                menuUri: "",
                parentId: "",
                status: 1
            },

            //增加菜单表单验证
            addFormRules: {
                menuId: [
                    { required: true, message: '请输入菜单编码' },
                    { min: 1, max: 30, message: '长度在 1 到 30 个字符' }
                ],
                menuName: [
                    { required: true, message: '请输入菜单名称' },
                    { min: 1, max: 30, message: '长度在 1 到 30 个字符' }
                ],
                institutionTypeId: [
                    { required: true, message: '请选择机构类型' },
                ],
                menuSeq: [
                    { validator: checkMenuSeq },
                ]

            },


        }
    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps'
        })
    },
    methods: {
        //查询列表
        search_smt() {
            this.menuSearch.page = 1;
            this.getMenuList();
        },

        //增加菜单按钮
        addForm_btn(id) {
            this.getInstList();
            this.dialogAddForm = true;
            this.menuId = '';
            this.addForm = {
                institutionTypeId: "",
                isOpen: '1',
                menuIco: "",
                menuId: "",
                menuName: "",
                menuSeq: '',
                menuUri: "",
                parentId: "",
                status: '1'
            }
        },

        //菜单列表查询
        getMenuList() {

            this.menu_list_loading = true;//test
            this.axios.post(this.myConfig.code.auth + '/api/menu/list', { body: this.menuSearch }).then(response => {
                let data = response.data;
                this.total = data.body.total;
                if (data.head.code = "10000") {
                    this.menu_list_loading = false;
                    this.menuData = data.body.rows;
                }
            }).catch(error => {
                console.log(error)
            })
        },

        //查询所有机构类型
        getInstList() {
            let that = this;
            that.axios.post(this.myConfig.code.auth + '/api/inst/list', {}).then(response => {
                let data = response.data;
                if (data.head.code = "10000") {
                    that.instList = data.body.rows;
                }
            }).catch(error => {
                console.log(error)
            })
        },
        instChecked(val) {
            this.instTypeId = val;
            this.getParentList();

        },
        //所有父级菜单
        getParentList() {
            this.axios.post(this.myConfig.code.auth + '/api/menu/parent', { body: { instTypeId: this.instTypeId } }).then(response => {
                let data = response.data;
                if (data.head.code = "10000") {
                    this.parentList = data.body.rows;
                }
            })
        },

        //查看菜单详情
        getMenuDetail(id) {
            this.axios.post(this.myConfig.code.auth + '/api/menu/detail', { body: { menuId: id } }).then(response => {
                let data = response.data;
                data.body.status = data.body.status.toString();
                data.body.isOpen = data.body.isOpen.toString();
                this.addForm = data.body;
                this.menuId = this.addForm.menuId;
                this.instTypeId = this.addForm.institutionTypeId;
                this.getParentList();
                this.dialogAddForm = true;
            })
        },

        //菜单管理表单提交
        submit_addMenu() {
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    if (this.menuId == '') {//新增菜单
                        this.axios.post(this.myConfig.code.auth + '/api/menu/save', { body: this.addForm }).then(response => {
                            let data = response.data;
                            if (data.head.code === '10000') {//新增成功
                                this.getMenuList();
                                this.dialogAddForm = false;
                            } else {//新增失败
                                this.$message({
                                    type: "error",
                                    message: data.body.sub_msg,
                                    duration: 1000
                                })
                            }
                        }).catch(error => {
                            console.log(error)
                        })
                    } else {//修改菜单
                        this.axios.post(this.myConfig.code.auth + '/api/menu/update', { body: this.addForm }).then(response => {
                            let data = response.data;
                            if (data.head.code === '10000') {//新增成功
                                this.getMenuList();
                                this.dialogAddForm = false;
                            } else {//新增失败
                                this.$message({
                                    type: "error",
                                    message: data.body.sub_msg,
                                    duration: 1000
                                })
                            }
                        }).catch(error => {
                            console.log(error)
                        })
                    }
                }
            })
        },

        //修改菜单
        doUpdate(id) {
            this.getInstList();
            this.getMenuDetail(id);
        },
        //删除菜单
        doDelete(id) {
            let that = this;
            this.$confirm('您确定删除此菜单?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                closeOnClickModal: false
            }).then(() => {
                that.axios.post(this.myConfig.code.auth + '/api/menu/delete', { body: { menuId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//删除成功
                        that.getMenuList();
                        that.$message({
                            type: "success",
                            message: '删除成功',
                            duration: 1000
                        })
                    } else {//新增失败
                        that.$message({
                            type: "error",
                            message: data.body.sub_msg,
                            duration: 1000
                        })
                    }
                }).catch(error => {
                    console.log(error)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除',
                    duration: 1000
                });
            });
        },

        //跳转到操作管理
        doAction(id) {
            this.$router.push('/auth/menuMgmt/actMgmt/' + id);
        },
        //分页
        handleCurrentChange(val) {
            this.menuSearch.page = val;
            this.getMenuList();
        }
    },
    created() {

        //菜单列表初始化
        this.getMenuList();
        this.getInstList()


    }
}
</script>




