//404页面

import SysMain from '@/components/sys/main.vue'
//404 component
import NotFoundComponent from '@/components/NotFoundComponent.vue';

let router_404 = {
    path: '*',
    component: SysMain,
    redirect: '/error/404',
    children: [
        {
            path: '*',
            component: NotFoundComponent
        }
    ]
} 
export default router_404


