<template>
  <div class="welcome"> 
      <img src="../../assets/common/images/notice.jpg" alt="">
       <!-- 商品有效期提示 -->
        <el-dialog title="提示" :visible.sync="validityTerm.dialogValidityDate" size="tiny"  class="dialog-validitydate" top="30vh">
        <div class="txt">您在售的产品中有 <span>{{validityTerm.num}}</span>个即将过期的产品，产品过期将自动下架，请尽快处理！</div>
        <span slot="footer" class="dialog-footer">
            <el-button size="small" @click="validityTerm.dialogValidityDate = false">取 消</el-button>
            <el-button size="small" type="primary" @click="doction('batchTerm')">批量处理</el-button>
            <el-button size="small" type="primary" @click="doction('allTerm')">全部处理</el-button>
        </span>
        </el-dialog>
      
  </div>
</template>



<script>
export default {
  data(){
      return {
          //商品有效期
          validityTerm:{
            dialogValidityDate:false,
            num:0
          }
         
      }
  },
  methods:{
    doction(type){
        localStorage.setItem('act',JSON.stringify({"active":{"key":["SJ_GOODS_M"],"sub_key":"/goods/list"}}));
        switch(type){
            case "batchTerm":
                this.$store.dispatch('setNoticeTerm',"batchTerm") ;  
                localStorage.setItem('hrf', JSON.stringify({ uri:"/goods/list?notice=batchTerm", time: new Date().getTime() }))
            break;
            case "allTerm":
                this.$store.dispatch('setNoticeTerm',"allTerm") ;
                localStorage.setItem('hrf', JSON.stringify({ uri:"/goods/list?notice=allTerm", time: new Date().getTime() }))
            break;
            // {"active":{"key":["SJ_GOODS_M"],"sub_key":"/goods/list"}}
        }
        
        this.$router.push({ path: '/main/iframe', query: { tm: new Date().getTime() } });
    },
    //查询商品有效期
    getGoodsValidityDate() {
        this.axios.post("/proxy/fv/supplier/goods.php?act=getOutTimeGoods", {}).then(res => {
            let data=res.data;
            if(data.body.number){
                this.validityTerm.num=data.body.number;
                this.validityTerm.dialogValidityDate=true;
            }
        });
    },
  },
  created(){     
    if (JSON.parse(window.localStorage.getItem("userInfo")).institutionTypeId === "SYSTEM_OPERATOR") {
      this.userType = "admin";    
    } else {
      this.userType = "supplier";  
      this.getGoodsValidityDate();
    }
  }
}
</script>

<style lang="less" scoped>
.welcome{
    text-align: center;
}
.welcome img{
    display: inline-block;
}
</style>

<style lang="less">
.dialog-validitydate{
  .el-dialog__footer{
    text-align:center;
  }
  .txt{
    span{
      color:#ff4949;
      text-decoration:underline;
      font-size:24px;
    }
  }
  .list{
    margin-left: 30px;
    margin-bottom: 15px;
  }
}
</style>





