import Vue from 'vue';
import Vuex from 'vuex';
import getters from './getters';
import actions from './actions';
Vue.use(Vuex);

//state
const state = {
    //权限
    authority: {},
    authSelect: "",
    authOps: [],
    //折叠面板
    collapse: 4,
    //返回顶部
    scrollTop: 1,
    //记住翻译的状态
    translate: {
        manage: '-1',
        myTask: '-1'
    },
    //记住翻译搜索状态
    translateSearch: {
        auto: {}, //审核翻译列表
        allotTask: {}, //翻译任务领取
        myTask: {}, //我的任务管理
        task: {}, //翻译任务管理
    },
    //首页公告
    notice: {
        //商品有效期公告
        batchTerm: false,
        allTerm: false
    },
    goods: {
        batchIds: []
    }
}

//mutations
const mutations = {
    //设置权限列表
    setAuthority(state, authority) {
        state.authority = authority
    },
    setAuthSelect(state, auth) { //存储一级菜单
        state.authSelect = auth;
    },
    //操作权限
    setAuthOps(state, ops) {
        state.authOps = ops;
    },
    setCollapse(state) {
        state.collapse = state.collapse == 4 ? 1 : 4;
    },
    setScrollTop(state, h) {
        state.scrollTop = h;
    },
    setTranslateManage(state, obj) {
        state.translate.manage = obj;
    },
    setTranslateMyTask(state, obj) {
        state.translate.myTask = obj;
    },
    setTranslateSearch(state, data) {
        state.translateSearch[data.type] = data.data;
    },
    // 公告处理 商品有效期公告处理
    setNoticeTerm(state, type) {
        state.notice[type] = true;

    },
    // 批量修改商品
    setGoodsBatch(state, data) {
        state.goods.batchIds = data;
        console.log(state, 55555555555)
    },
}

export default new Vuex.Store({ state, mutations, getters, actions })


