// 财务报表模块
import SysMain from '@/components/sys/main.vue';

//财务报表 start....
const FACMS = r => require.ensure([], () => r(require('@/components/sys/report/financial/fa-cms.vue')), 'report')
const FACost = r => require.ensure([], () => r(require('@/components/sys/report/financial/fa-cost.vue')), 'report')
const FAPayRes = r => require.ensure([], () => r(require('@/components/sys/report/financial/fa-pay-res.vue')), 'report')
const FASettle = r => require.ensure([], () => r(require('@/components/sys/report/financial/fa-settle.vue')), 'report')
const FAFreight = r => require.ensure([], () => r(require('@/components/sys/report/financial/fa-freight.vue')), 'report')
const FAExchange = r => require.ensure([], () => r(require('@/components/sys/report/financial/fa-exchange.vue')), 'report')

//运营统计报表
const OpStatsCategory = r => require.ensure([], () => r(require('@/components/sys/report/operation-stats/category.vue')), 'report')
const OpStatsBrand = r => require.ensure([], () => r(require('@/components/sys/report/operation-stats/brand.vue')), 'report')
const OpStatsSPU = r => require.ensure([], () => r(require('@/components/sys/report/operation-stats/spu.vue')), 'report')
const OpStatsSupplier = r => require.ensure([], () => r(require('@/components/sys/report/operation-stats/supplier.vue')), 'report')
const OpStatsDaily = r => require.ensure([], () => r(require('@/components/sys/report/operation-stats/daily.vue')), 'report')

const OpOrderDetail = r => require.ensure([], () => r(require('@/components/sys/report/operation/order-detail.vue')), 'report')
const OpGoods = r => require.ensure([], () => r(require('@/components/sys/report/operation/goods.vue')), 'report')
const OpProduct = r => require.ensure([], () => r(require('@/components/sys/report/operation/product.vue')), 'report')
const OpSupplier = r => require.ensure([], () => r(require('@/components/sys/report/operation/supplier.vue')), 'report')

const CseLogistics = r => require.ensure([], () => r(require('@/components/sys/report/customer/logistics.vue')), 'report')
const CseRecordSKU = r => require.ensure([], () => r(require('@/components/sys/report/customer/record-sku.vue')), 'report')

//财务报表 start.... 财务佣金结算
let router_FACMS = {
    path: 'fa-cms',
    name: 'report-fa-cms',
    component: FACMS
}
//财务费用结算单
let router_faCost = {
    path: 'fa-cost',
    name: 'report-fa-cost',
    component: FACost
}
// 付款申请单
let router_faSettle = {
    path: 'fa-settle',
    name: 'report-fa-settle',
    component: FASettle
}
// 付款申请单
let router_faPayRes = {
    path: 'fa-pay-res',
    name: 'report-fa-pay-res',
    component: FAPayRes
}
// 运费报表
let router_faFreight = {
    path: 'fa-freight',
    name: 'report-fa-freight',
    component: FAFreight
}
// 运费报表
let router_faExchange = {
    path: 'fa-exchange',
    name: 'report-fa-exchange',
    component: FAExchange
}

//运用报表 start..... 商品销售明细
let router_operation_orderDetail = {
    path: 'op-orderdetail',
    name: 'report-op-order-detail',
    component: OpOrderDetail
}
//SPU商品列表
let router_operation_goods = {
    path: 'op-goods',
    name: 'report-op-goods',
    component: OpGoods
}
//SKU商品列表
let router_operation_product = {
    path: 'op-product',
    name: 'report-op-product',
    component: OpProduct
}
//商家列表
let router_operation_supplier = {
    path: 'op-supplier',
    name: 'report-op-supplier',
    component: OpSupplier
}

/*    
    运营统计报表 start....
*/
//每日统计
let router_operation_stats_daily = {
    path: 'op-stats-daily',
    name: 'report-op-stats-daily',
    component: OpStatsDaily
}

let router_operation_stats_category = {
    path: 'op-stats-category',
    name: 'report-op-stats-category',
    component: OpStatsCategory
}
//品牌销售统计
let router_op_stats_brand = {
    path: 'op-stats-brand',
    name: 'report-op-stats-brand',
    component: OpStatsBrand
}
//SPU销售统计
let router_op_stats_spu = {
    path: 'op-stats-spu',
    name: 'report-op-stats-spu',
    component: OpStatsSPU
}
//商家销售统计
let router_op_stats_supplier = {
    path: 'op-stats-supplier',
    name: 'report-op-stats-supplier',
    component: OpStatsSupplier
}

//客服报表 start.... 订单物流信息
let router_customer_logistics = {
    path: 'cse-logistics',
    name: 'report-cse-logistics',
    component: CseLogistics
}
let router_customer_recordsku = {
    path: 'cse-recordsku',
    name: 'report-cse-recordsku',
    component: CseRecordSKU
}

let router_report = {
    path: 'report',
    name: 'report',
    component: SysMain,
    children: [
        router_FACMS,
        router_faCost,
        router_faPayRes,
        router_faSettle,
        router_faFreight,
        router_faExchange,
        router_operation_orderDetail,
        router_operation_goods,
        router_operation_product,
        router_operation_supplier,
        router_customer_logistics,
        router_operation_stats_daily,
        router_operation_stats_category,
        router_op_stats_brand,
        router_op_stats_spu,
        router_op_stats_supplier,
        router_customer_recordsku
    ]
}

export default router_report;


